import React from "react";
import ReactTooltip from "react-tooltip";
import SelectClassSubject from "../SelectRoutinClasses/SelectClassSubject";

function UpdateRoutineElement({ time, routine, editRoutine, deleteRoutine }) {
    return (
        <div>
            <div className="routineRows">
                <div className="routineColsIndex" style={{ width: "120px" }}>
                    <ReactTooltip
                        id="DeleteRowFromTable"
                        place="bottom"
                        type="dark"
                        effect="solid"
                    >
                        Delete Row
                    </ReactTooltip>
                    {time}
                    {editRoutine && (
                        <div
                            data-tip
                            data-for="DeleteRowFromTable"
                            className="routineColsIndex_Delete"
                            onClick={() => deleteRoutine(time)}
                        >
                            X
                        </div>
                    )}
                </div>

                {routine.map((routine) => (
                    <div key={routine.day} className="routineCols">
                        <SelectClassSubject
                            time={time}
                            day={routine.day}
                            courseId={routine.course_id}
                        />
                    </div>
                ))}
            </div>
        </div>
    );
}

export default UpdateRoutineElement;
