import axios from "axios";
import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { loadStorage } from "../../../utils/persistLocalStorage";
import DevReportFormPopup from "../../../components/devReportFormpopup/DevReportFormPopup";
import Header from "../../TeacherPage/components/header/Header";
import "./developmentPage.scss";
import { GET_N_REPORT_URL, UPVOTE_REPORT_URL } from "../../../utils/urls";

function DevelopmentPage() {
    const user = loadStorage("user");
    const navigate = useNavigate();
    const [createReport, setCreateReport] = useState(false);
    const [reload, setReload] = useState(false);
    const [reports, setReports] = useState([]);
    const [totalPage, setTotalPage] = useState(1);
    const [page, setPage] = useState(1);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        if (!user) {
            navigate("/auth/login");
        } else {
            axios({
                method: "get",
                url: GET_N_REPORT_URL + "/" + `${page}`,
                headers: {
                    "Content-Type": "application/json",
                    "x-auth-token": user.token,
                },
            })
                .then((res) => {
                    setReports(res.data.data.reportData);
                    setTotalPage(res.data.data.totalPage);
                    setIsLoading(false);
                    console.log(res.data.data.reportData);
                })
                .catch((err) => {
                    console.log(err);
                    setIsLoading(false);
                });
        }
    }, [page, reload]);

    const handleUpvote = (e, id) => {
        e.preventDefault();

        setIsLoading(true);
        axios({
            method: "patch",
            url: UPVOTE_REPORT_URL + `\/${id}`,
            headers: {
                "Content-Type": "application/json",
                "x-auth-token": user.token,
            },
        })
            .then((res) => {
                setReload(!reload);
                setIsLoading(false);
            })
            .catch((err) => {
                console.log(err);
                setIsLoading(false);
            });
    };

    const handleDownvote = (e, id) => {
        e.preventDefault();

        setIsLoading(true);
        axios({
            method: "patch",
            url: UPVOTE_REPORT_URL + `\/${id}`,
            headers: {
                "Content-Type": "application/json",
                "x-auth-token": user.token,
            },
        })
            .then((res) => {
                setReload(!reload);
                setIsLoading(false);
            })
            .catch((err) => {
                console.log(err);
                setIsLoading(false);
            });
    };

    const getBGColor = (report) => {
        if (
            report.is_reported &&
            !report.is_acknowledged &&
            !report.is_in_progress &&
            !report.is_completed
        ) {
            return "#EF7B6B";
        } else if (
            report.is_acknowledged &&
            !report.is_in_progress &&
            !report.is_completed
        ) {
            return "#FFB248";
        } else if (report.is_in_progress && !report.is_completed) {
            return "#F4DE33";
        } else if (report.is_completed) {
            return "#81CA72";
        }
    };

    return (
        <div className="developmentContainer">
            <div className="header_container_dev">
                <Header />
            </div>

            <div className="developmentContainerBody">
                <div className="leftContainer">
                    <h2 className="heading_text">Reporting Instructions</h2>
                    <p className="subheading_text">
                        This is a page for developers and users for sharing
                        issues and requirements.You can post issues and your
                        feedback about the website and its features as well as
                        you can request new features.
                    </p>
                    <ul type="dot">
                        <li>
                            Write about an issue using the{" "}
                            <b>Create New Report</b> button.
                        </li>
                        <li>
                            You can upvote and downvote an issue using the{" "}
                            <b>UP</b>
                            and <b>DOWN</b> arrow buttons.
                        </li>
                        <li>
                            Posts with <b>Highest Ratio</b> of upvote and
                            downvote will be prioritized.
                        </li>
                        <li>
                            Most <b>prioritized</b> posts will be on the{" "}
                            <b>to do list</b> of the developers.
                        </li>
                        <li>
                            You can check the <b>status</b> of the Issue by the{" "}
                            <b>color</b> on the <b>top right corner</b>.
                        </li>
                        <li>
                            In the case of multiple posts regarding the same
                            issue, least upvoted issues will be deleted by the
                            system.
                        </li>
                    </ul>
                </div>
                <hr />
                <div className="midContainer">
                    <h1
                        style={{
                            color: "#46178f",
                            // backgroundColor: "red",
                            borderRadius: "5px",
                            padding: "10px",
                            boxShadow:
                                "rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px",
                        }}
                    >
                        Posts
                    </h1>
                    <div className="postHolderContainer">
                        {isLoading ? (
                            <div>Loading...</div>
                        ) : (
                            <div className="reportList">
                                <div>
                                    {reports.map((report, index) => {
                                        return (
                                            <div
                                                className="reportListItem"
                                                key={index}
                                            >
                                                <div className="voteInfo">
                                                    <div
                                                        className="voteIcon"
                                                        onClick={(e) => {
                                                            handleUpvote(
                                                                e,
                                                                report.id
                                                            );
                                                        }}
                                                    >
                                                        
                                                    </div>
                                                    <div className="voting_count">
                                                        {report.upvote_count}
                                                    </div>
                                                    <hr
                                                        style={{
                                                            width: "40%",
                                                        }}
                                                    />
                                                    <div className="voting_count">
                                                        {report.downvote_count}
                                                    </div>
                                                    <div
                                                        className="voteIcon"
                                                        onClick={(e) => {
                                                            handleDownvote(
                                                                e,
                                                                report.id
                                                            );
                                                        }}
                                                    >
                                                        
                                                    </div>
                                                </div>
                                                <div className="reportInfo">
                                                    <div className="reportHeader">
                                                        <span>
                                                            {report.topic}
                                                        </span>
                                                        <span
                                                            style={{
                                                                flex: 1,
                                                            }}
                                                        />

                                                        <div
                                                            className="progress_indicator"
                                                            style={{
                                                                backgroundColor:
                                                                    getBGColor(
                                                                        report
                                                                    ),
                                                            }}
                                                        />
                                                    </div>

                                                    <div className="reportBody">
                                                        {report.details}
                                                    </div>
                                                    <div className="reportTime">
                                                        Posted on:{" "}
                                                        {new Date(
                                                            report.created_at
                                                        ).toString()}
                                                    </div>
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        )}
                    </div>
                    <div className="pagination">
                        {totalPage > 1 ? (
                            <div className="paginationContainer">
                                <button
                                    className="paginationButton"
                                    onClick={() => {
                                        if (page > 1) {
                                            setPage(page - 1);
                                        }
                                    }}
                                >
                                    {"<"} Previous
                                </button>{" "}
                                <button
                                    className="paginationButton"
                                    onClick={() => {
                                        if (page < totalPage) {
                                            setPage(page + 1);
                                        }
                                    }}
                                >
                                    Next {">"}
                                </button>
                            </div>
                        ) : null}
                    </div>
                </div>
                <hr />
                <div className="rightContainer">
                    <div className="Icon_desc_container">
                        <div className="Icon_element">
                            <div
                                className="Icon_circle"
                                style={{ background: "#EF7B6B" }}
                            />
                            Report Submitted
                        </div>
                        <div className="Icon_element">
                            <div
                                className="Icon_circle"
                                style={{ background: "#FFB248" }}
                            />{" "}
                            Issue Acknowledged
                        </div>
                        <div className="Icon_element">
                            <div
                                className="Icon_circle"
                                style={{ background: "#F4DE33" }}
                            />{" "}
                            Working On
                        </div>
                        <div className="Icon_element">
                            <div
                                className="Icon_circle"
                                style={{ background: "#81CA72" }}
                            />{" "}
                            Issue Fixed
                        </div>
                    </div>

                    <div className="Button_desc_container">
                        <div
                            className="Button_element"
                            onClick={() => {
                                setCreateReport(true);
                            }}
                        >
                            Create New Report
                        </div>
                    </div>
                </div>
            </div>

            {createReport && (
                <DevReportFormPopup
                    setCreateReport={setCreateReport}
                    setReload={setReload}
                    reload={reload}
                />
            )}
        </div>
    );
}

export default DevelopmentPage;
