import React, { useEffect, useState } from "react";
import "./modMaterialsItems.scss";

const ModMaterialsItems = (props) => {
	const {
		isheader,
		subject_name,
		chapter_name,
		chapter_serial,
		file_name,
		file_type,
		size,
		updated_at,
		handleDelete,
		handleDownload,
	} = props;
	console.log(props);
	const [header, setHeader] = useState(false);

	useEffect(() => {
		setHeader(isheader);
	}, [isheader]);

	return (
		<div>
			{header ? (
				<div className="modMaterialsItemsContainer">
					<div className="headerItem">Subject Name</div>
					<div className="headerItem">Chapter Name</div>
					<div className="headerItem">Chapter serial</div>
					<div className="headerItem">File Name</div>
					{/* <div className="headerItem">File type</div> */}
					<div className="headerItem">Size</div>
					<div className="headerItem">Updated At</div>
					<div className="headerItem">Actions</div>
				</div>
			) : (
				<div className="modMaterialsItemsContainer">
					<div>{subject_name}</div>
					<div>{chapter_name}</div>
					<div>{chapter_serial}</div>
					<div>
						{file_name?.length > 30
							? file_name.slice(0, 25) +
							  "...." +
							  file_name.split(".")[
									file_name.split(".").length - 1
							  ]
							: file_name}
					</div>
					{/* <div>{file_type}</div> */}
					<div>{(size / 1000).toFixed(2)}MB</div>
					<div>{updated_at}</div>

					<div className="BtnContainer">
						<div className="DeleteBtn" onClick={handleDelete}>
							Delete
						</div>
						<div className="DeleteBtn" onClick={handleDownload}>
							Download
						</div>
					</div>
				</div>
			)}
		</div>
	);
};

export default ModMaterialsItems;
