import React from "react";
import "./course.scss";

const Course = ({ item }) => {
	return (
		<div className="courseContainer">
			<div className="courseContainerText">
				<div className="courseContainerTextName">{item.name}</div>
				<div className="courseContainerTextTitle">{item.title}</div>
				<div className="courseContainerTextPrice">{item.price}</div>
			</div>

			<img
				className="courseContainerImg"
				src={item.img}
				alt={item.name}
			/>
		</div>
	);
};

export default Course;
