import axios from "axios";
import {
	GET_ALL_SUBJECTS_BY_CLASS_ID,
	GET_ENABLED_SUBJECTS_BY_CLASS_ID,
	GET_AVAILABLE_SUBJECTS_BY_CLASS_ID,
} from "../utils/urls";

export const getAllSubjectsByClassID = (data) => {
	return axios({
		method: "get",
		url: GET_ALL_SUBJECTS_BY_CLASS_ID + `/${data.classId}`,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
	});
};

