import React from "react";
import { useNavigate } from "react-router-dom";
import "./formList.scss";

function FormList() {
    const navigate = useNavigate();
    const form_data = [
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
        {
            name: "Form 1",
            uuid: "1dKUiiYSDg4gWjG89p5-JTmKvM5-cAz_Z1hRaBzv9ZWE",
            thumbnail:
                "https://ssl.gstatic.com/docs/templates/thumbnails/forms-blank-googlecolors.png",
        },
    ];

    const handleOnClick = (uuid) => {
        console.log(uuid);
        navigate(`/forms/${uuid}`);
    };

    return (
        <div className="form_list_container">
            <div className="title">
                <span>Recent Forms</span>
            </div>
            <div className="form_list">
                {form_data.map((form, index) => {
                    return (
                        <div
                            className="form_item"
                            key={index}
                            onClick={() => {
                                handleOnClick(form.uuid);
                            }}
                        >
                            <div className="form_thumbnail">
                                <img src={form.thumbnail} alt="template" />
                            </div>

                            <div className="form_footer">
                                <div className="form_name">
                                    <span>{form.name}</span>
                                </div>

                                <div className="form_options">
                                    <div className="form_options_left">
                                        <div className="form_icon">
                                            <img
                                                src="https://www.gstatic.com/images/branding/product/1x/forms_48dp.png"
                                                alt="forms logo"
                                            />
                                        </div>
                                        <div className="form_opened">
                                            <span>Opened 13 Jun 2022</span>
                                        </div>
                                    </div>
                                    <div className="form_options_right">
                                        <div className="form_more">
                                            <i className="fas fa-ellipsis-v"></i>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}

export default FormList;
