import React, { useEffect, useState } from "react";
import "./navbar.scss";

import {
    Chat,
    ClassRounded,
    HomeOutlined,
    LibraryBooks,
} from "@material-ui/icons";

function Navbar({ setActivePage, activePage }) {
    const [activeNav, setActiveNav] = useState("#homePage");
    const btn1OnClick = (tag, goto) => {
        setActiveNav(tag);
        setActivePage(tag);
    };

    useEffect(() => {
        setActiveNav(activePage);
    }, [activePage]);

    //btnOnClick with param

    return (
        <nav>
            <div
                onClick={() => btn1OnClick("#homePage")}
                className={activeNav === "#homePage" ? "active" : ""}
            >
                <HomeOutlined style={{ color: "#fff" }} />
            </div>

            <div
                onClick={() => btn1OnClick("#classPage")}
                className={activeNav === "#classPage" ? "active" : ""}
            >
                <ClassRounded style={{ color: "#fff" }} />
            </div>

            <div
                onClick={() => btn1OnClick("#CreateFormPage")}
                className={activeNav === "#CreateFormPage" ? "active" : ""}
            >
                <LibraryBooks style={{ color: "#fff" }} />
            </div>
            <div
                onClick={() => btn1OnClick("#chatPage")}
                className={activeNav === "#chatPage" ? "active" : ""}
            >
                <Chat style={{ color: "#fff" }} />
            </div>
        </nav>
    );
}

export default Navbar;
