import {
    ArrowDropDownTwoTone,
    DragIndicator,
    Subject,
    SubjectOutlined,
} from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import ReactTooltip from "react-tooltip";
import { getModeratorCourseInfo } from "../../../../../api/course.api";
import { MAT_URL } from "../../../../../utils/urls";
import ShowChaptersTable from "./ShowChaptersTable";

const ShowSubjectTable = ({
    showSubject,
    subjectItem,
    subjectId,
    token,
    classItem,
}) => {
    const [showTopic, setShowTopic] = useState(false);
    const [chapters, setChapters] = useState([]);
    const [selectedSubjectTable, setSelectedSubjectTable] = useState(false);
    const [showDropdown, setShowDropdown] = useState(false);

    const editCourse = (id) => {
        console.log(id);
    };

    const deleteCourse = (id) => {
        console.log(id);
    };

    useEffect(() => {
        console.log(subjectItem);
        if (subjectId) {
            getModeratorCourseInfo({ subjectId, token: token })
                .then((res) => {
                    console.log("=>", res.data.data);
                    setChapters(res.data.data.chapters);
                })
                .catch((err) => {
                    console.log(err);
                });
        }
    }, []);

    return (
        // <div className={showTopic ? "ShowSubjectTableContainer" : ""}>
        <div>
            <div
                className={
                    selectedSubjectTable
                        ? "subject_table subject_table_selected"
                        : "subject_table"
                }
            >
                <div className="vertical_bar"></div>
                {/* <div className="subject_table_icon">
                    <Subject />
                 
                </div> */}
                <div
                    className="SubjectRowMid"
                    onClick={() => {
                        setShowTopic(!showTopic);
                        setSelectedSubjectTable(!selectedSubjectTable);
                        setShowDropdown(false);
                    }}
                >
                    {/* <SubjectOutlined
                        style={{
                            fontSize: "40px",
                            paddingLeft: "20px",
                        }}
                    /> */}

                    <div className="subject_table_icon">
                        <img
                            style={{ height: "25px" }}
                            src={
                                MAT_URL +
                                subjectItem.subject.subject_icon.file_path
                            }
                            alt="course_icon"
                        />
                    </div>

                    <div className="subject_table_name">
                        {subjectItem.subject.subject_name}
                    </div>

                    {/* <div className="subject_table_cover">
                        <img
                            src={
                                MAT_URL +
                                subjectItem.subject.subject_cover.file_path
                            }
                            alt="course_cover"
                        />
                    </div> */}
                    {/* 
                    <div
                        className="subject_table_courseCode"
                        data-tip
                        data-for="CourseCodeTip"
                    >
                        {subjectItem.course_code}
                    </div> */}
                </div>
                <div className="subject_table_actions">
                    <div className="navbar">
                        <div className="dropdown">
                            <button className="dropbtn">
                                {/* <DragIndicator
                                    className="class_Operations_dropdown"
                                    onClick={(event) => {
                                        console.log("dropdown");
                                        setShowDropdown(!showDropdown);
                                    }}
                                /> */}
                                <ArrowDropDownTwoTone
                                    className="class_Operations_dropdown"
                                    style={{
                                        fontSize: "35px",
                                    }}
                                    onClick={(event) => {
                                        console.log("dropdown");
                                        setShowDropdown(!showDropdown);
                                    }}
                                />
                            </button>
                            {showDropdown && (
                                <div className="dropdown-content">
                                    <div
                                        className="dropdown-content-option"
                                        onClick={() => {
                                            editCourse(subjectItem.subject.id);
                                        }}
                                    >
                                        Edit course
                                    </div>
                                    <div
                                        className="dropdown-content-option"
                                        onClick={() => {
                                            deleteCourse(
                                                subjectItem.subject.id
                                            );
                                        }}
                                    >
                                        Delete course
                                    </div>
                                    <div
                                        className="dropdown-content-option"
                                        onClick={() => {
                                            console.log("Add New course");
                                        }}
                                    >
                                        Add New course
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>
            {showSubject && showTopic && (
                <div className="topic_table">
                    {JSON.stringify(chapters).length < 3 && (
                        <div className="topic_table_no_chapters">
                            No Chapters Available
                        </div>
                    )}

                    {chapters.map((chapter) => {
                        return (
                            <div>
                                <ShowChaptersTable
                                    key={chapter}
                                    chapter={chapter}
                                    classItem={classItem}
                                    subjectItem={subjectItem}
                                />
                            </div>
                        );
                    })}
                </div>
            )}

            <ReactTooltip
                id="CourseCodeTip"
                place="bottom"
                type="light"
                effect="solid"
            >
                Course Code
            </ReactTooltip>
        </div>
    );
};

export default ShowSubjectTable;
