import React, { useEffect, useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./../popUp.scss";
import { getEnabledSubjectsByClassID } from "../../../../../api/subject.api";
import ModButton from "../../../components/ModButton/ModButton";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { CREATE_SUBJECT } from "../../../../../utils/urls";

function CreateSubjectPopUp(props) {
	const { setCreateSubjectPopUpOpen, classId, setSubjects } = props;

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const [inputs, setInputs] = useState({
		subject_name: "",
		class_id: classId,
	});
	const [selectedFile1, setSelectedFile1] = useState();
	const [selectedFile2, setSelectedFile2] = useState();
	const [subjectNameError, setSubjectNameError] = useState("");

	const handleInputChange = (e) => {
		const name = e.target.name;
		const value = e.target.value;

		setInputs({ ...inputs, [name]: value });
	};

	const iconChnageHandler = (event) => {
		setSelectedFile1(event.target.files[0]);
	};

	const coverChangeHandler = (event) => {
		setSelectedFile2(event.target.files[0]);
	};

	const createSubject = () => {
		console.log("create subject");
		// console.log(inputs);
		// console.log(selectedFile1);
		// console.log(selectedFile2);

		if (inputs.subject_name === "") {
			setSubjectNameError("Subject name is required");
		} else if (selectedFile1 === undefined || selectedFile2 === undefined) {
			setSubjectNameError("Subject icon and cover is required");
		} else {
			setSubjectNameError("");
			const formData = new FormData();
			formData.append("subject_name", inputs.subject_name);
			formData.append("class_id", inputs.class_id);
			formData.append("subject_icon", selectedFile1);
			formData.append("subject_cover", selectedFile2);

			axios({
				method: "post",
				url: CREATE_SUBJECT,
				headers: {
					"x-auth-token": user.token,
					"Content-Type": "multipart/form-data",
				},
				data: formData,
			})
				.then((res) => {
					console.log(res);
					setCreateSubjectPopUpOpen(false);
					getEnabledSubjectsByClassID({
						classId,
						token: user.token,
					})
						.then((res) => {
							console.log(res.data.data);
							setSubjects(res.data.data);
						})
						.catch((err) => {
							console.log(err);
						});
				})
				.catch((err) => {
					console.log(err);
				});
		}
	};

	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the create course pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setCreateSubjectPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>
			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<div className="form_field">
						<label>Subject Name</label>
						<input
							type="text"
							name="subject_name"
							placeholder="Subject Name"
							required
							autoFocus
							className="ModeratorFormInput"
							onChange={handleInputChange}
						/>
					</div>
				</div>

				<div className="form_row">
					<div className="form_field">
						<label>Subject Icon</label>
						<input
							type="file"
							accept="image/png, image/gif, image/jpeg"
							name="subject_icon"
							required
							className="ModeratorFormInput"
							onChange={iconChnageHandler}
						/>
					</div>
				</div>

				<div className="form_row">
					<div className="form_field">
						<label>Subject Cover</label>
						<input
							type="file"
							accept="image/png, image/gif, image/jpeg"
							name="subject_cover"
							required
							className="ModeratorFormInput"
							onChange={coverChangeHandler}
						/>
					</div>
				</div>
				<span className="error">{subjectNameError}</span>

				<div className="ModeratorFormBtns">
					<ModButton btnText={"Create"} onClick={createSubject} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setCreateSubjectPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default CreateSubjectPopUp;
