import React, { useEffect, useState } from "react";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import "./showClassesTable.scss";
import ReactTooltip from "react-tooltip";
import ShowSubjectTable from "./ShowSubjectTable";
import { getEnabledSubjectsByClassID } from "../../../../../api/subject.api";
import { DragIndicator } from "@material-ui/icons";

const ShowClasesTable = ({ classItem, classId }) => {
    const user = loadStorage("user");

    const [subjects, setSubjects] = useState([]);

    // animate table like drop down
    const [showSubject, setShowSubject] = useState(false);
    const [showDropdown, setShowDropdown] = useState(false);

    useEffect(() => {
        getEnabledSubjectsByClassID({ classId, token: user.token })
            .then((res) => {
                console.log("Enabled", res.data.data);
                setSubjects(res.data.data);
            })
            .catch((err) => {
                console.log(err);
            });
    }, [classId]);

    return (
        <div className="tables_container">
            <div className="class_table">
                <img
                    data-tip
                    data-for="IconTip"
                    className="class_icon"
                    src="https://cdn3.iconfinder.com/data/icons/indian-republic-day-26/512/787_Bangla_bangladesh_bangladeshi_business-512.png"
                    alt=""
                    onClick={() => {
                        setShowSubject(!showSubject);
                        setShowDropdown(false);
                    }}
                />
                {/* {classItem.id} */}
                <div
                    className="class_name"
                    onClick={() => {
                        setShowSubject(!showSubject);
                        setShowDropdown(false);
                    }}
                >
                    {classItem.class_name}
                </div>

                <div className="class_Operations">
                    <div className="navbar">
                        <div className="dropdown">
                            <button className="dropbtn">
                                <DragIndicator
                                    className="class_Operations_dropdown"
                                    onClick={(event) => {
                                        console.log("dropdown");
                                        setShowDropdown(!showDropdown);
                                    }}
                                />
                            </button>
                            {showDropdown && (
                                <div className="dropdown-content">
                                    <div className="dropdown-content-option">
                                        Edit Class
                                    </div>
                                    <div className="dropdown-content-option">
                                        Delete Class
                                    </div>
                                    <div className="dropdown-content-option">
                                        Add New Class
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                </div>
            </div>

            {showSubject && (
                <div className="subject_table_container">
                    {JSON.stringify(subjects).length < 3 && (
                        <div className="topic_table_no_chapters">
                            No Subjects Available
                        </div>
                    )}
                    {subjects.map((subjectItem) => {
                        return (
                            <ShowSubjectTable
                                key={subjectItem.id}
                                showSubject={showSubject}
                                subjectItem={subjectItem}
                                subjectId={subjectItem.subject.id}
                                token={user.token}
                                classItem={classItem}
                            />
                        );
                    })}
                </div>
            )}

            {/* {showSubject && showTopic && (
                <div className="topic_table">Topic table</div>
            )} */}

            <ReactTooltip
                id="IconTip"
                place="bottom"
                type="light"
                effect="solid"
            >
                Class Icon
            </ReactTooltip>
            <ReactTooltip
                id="CoverTip"
                place="bottom"
                type="light"
                effect="float"
            >
                Class Cover Image
            </ReactTooltip>
        </div>
    );
};

export default ShowClasesTable;
