import { Book, Link as LinkIcon } from "@material-ui/icons";
import React from "react";
import { useNavigate } from "react-router-dom";
import "./quizRow.scss";

const QuizRow = ({ quiz }) => {
	const navigate = useNavigate();

	const formatDateTime = (dueDateTime) => {
		const date = new Date(dueDateTime);
		const day = date.getDate();
		const monthList = [
			"Jan",
			"Feb",
			"Mar",
			"Apr",
			"May",
			"Jun",
			"July",
			"Aug",
			"Sep",
			"Oct",
			"Nov",
			"Dec",
		];
		const textMonth = monthList[date.getMonth()];
		const year = date.getFullYear();
		const hour = date.getHours();
		const minute = date.getMinutes();
		const ampm = hour >= 12 ? "PM" : "AM";
		const hour12 = hour % 12;
		const sHours = hour12 < 10 ? `0${hour12}` : hour12;
		const sMinutes = minute < 10 ? `0${minute}` : minute;
		const formattedTime = `${sHours}:${sMinutes} ${ampm}`;
		return `${day} ${textMonth}, ${year} ${formattedTime}`;
	};

	return (
		<div className="quizRow">
			<div className="quizRowTop">
				<div className="quizRowTopIcon">
					<Book />
				</div>
				<div className="quizRowTopTitle">{quiz.title}</div>
				<div className="quizRowTopDue">
					Due Date:{" "}
					{quiz.due_date_time
						? formatDateTime(quiz.due_date_time)
						: "N/A"}
				</div>
			</div>
			<div className="quizRowBody">
				<div className="quizRowBodyContainer">
					<div className="quizDesc">{quiz.details}</div>
					<div className="quizCreatedDate">
						Created on: {formatDateTime(quiz.created_at)}
					</div>
					<div
						className="quizPageLink"
						onClick={() => {
							navigate("/forms/" + quiz.uuid);
						}}
					>
						<LinkIcon /> Go to Quiz Page
					</div>
				</div>
			</div>
		</div>
	);
};

export default QuizRow;
