import React from "react";
import "./modClassListItem.scss";
import ModButton from "../ModButton/ModButton";

const ModClassListItem = (props) => {
	const { classItem, updateClass, deleteClass } = props;

	return (
		<div className="ModClassListItemContainer">
			<>
				<div className="ModClassListItemName">
					{classItem.class_name}
				</div>
				<div className="ModClassListItemActions">
					<ModButton
						btnText="Edit"
						onClick={() => {
							updateClass(classItem);
						}}
					/>
					<ModButton
						btnText="Delete"
						onClick={() => {
							deleteClass(classItem);
						}}
					/>
				</div>
			</>
		</div>
	);
};

export default ModClassListItem;
