import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { getAllCLasses } from "../../../../api/class.api";
import {
    getAllSubjectsByClassID,
    getAvailableSubjectsByClassID,
    getEnabledSubjectsByClassID,
} from "../../../../api/subject.api";
import CreateCoursePopUp from "../../components/popUps/createCoursePopUp/CreateCoursePopUp";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import { MAT_URL } from "../../../../utils/urls";
import "./classPage.scss";

function ClassPage() {
    const user = loadStorage("user");
    const navigate = useNavigate();

    const [createCoursePopUpOpen, setCreateCoursePopUpOpen] = useState(false);

    const idMessage = "create_course_popup";

    const [inputs, setInputs] = useState({
        classId: "",
    });
    const [classes, setClasses] = useState([]);
    const [enabledSubjects, setEnabledSubjects] = useState([]);
    const [availableSubjects, setAvailableSubjects] = useState([]);
    const [reload, setReload] = useState(false);

    useEffect(() => {
        if (!user) {
            navigate("/auth/login");
        } else {
            if (classes.length === 0) {
                getAllCLasses({ token: user.token })
                    .then((res) => {
                        // console.log(res.data.data);
                        setClasses(res.data.data);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
            // console.log(classes);
        }
    }, [classes]);

    useEffect(() => {
        setEnabledSubjects([]);
        classes.map((classItem) => {
            console.log(classItem.id);
            getEnabledSubjectsByClassID({
                token: user.token,
                classId: classItem.id,
            })
                .then((res) => {
                    console.log(res.data.data);
                    setEnabledSubjects((arr) => [...arr, ...res.data.data]);
                })
                .catch((err) => {
                    console.log(err);
                });
        });
        console.log("==>", enabledSubjects);
    }, [classes, reload]);

    const handleClassFilters = async (e) => {
        if (e.target.value !== "default") {
            const classId = e.target.value;
            console.log(classId);
            setInputs({ ...inputs, classId });

            getEnabledSubjectsByClassID({ token: user.token, classId })
                .then((res) => {
                    console.log("Enabled subjects", res.data.data);
                    setEnabledSubjects(res.data.data);
                })
                .catch((err) => {
                    console.log(err);
                });

            getAvailableSubjectsByClassID({ classId, token: user.token })
                .then((res) => {
                    console.log("Available subjects", res.data.data);
                    setAvailableSubjects(res.data.data);
                })
                .catch((err) => {
                    console.log(err);
                });
        } else {
            setInputs({ ...inputs, classId: "" });
            setEnabledSubjects([]);
            classes.map((classItem) => {
                console.log(classItem.id);
                getEnabledSubjectsByClassID({
                    token: user.token,
                    classId: classItem.id,
                })
                    .then((res) => {
                        console.log(res.data.data);
                        setEnabledSubjects((arr) => [...arr, ...res.data.data]);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            });
            console.log(enabledSubjects);
        }
    };

    const takeToChapterPage = (subjectId) => {
        navigate(`/course/${subjectId}`, {
            state: {
                prevPage: "/Dashboard",
            },
        });
    };

    return (
        <div className="classpage_container">
            <div className="classpage_title">
                <p className="classpage_title_text">Classes</p>
            </div>
            <div className="mid_section">
                <div className="mid_section_left">
                    <select
                        name="class"
                        onChange={handleClassFilters}
                        style={{
                            height: "50px",
                            marginTop: "10px",
                            border: "none",
                        }}
                        defaultValue="default"
                    >
                        <option value="default">All of your Classes</option>
                        {classes.length > 0 &&
                            classes.map((classItem) => {
                                return (
                                    <option
                                        key={classItem.id}
                                        value={classItem.id}
                                    >
                                         {classItem.class_name}
                                    </option>
                                );
                            })}
                    </select>

                    {inputs.classId && (
                        <div
                            className="create_course"
                            onClick={() => {
                                setCreateCoursePopUpOpen(true);
                            }}
                            type="button"
                            data-toggle="modal"
                            data-target={`#${idMessage}`}
                        >
                            Create Course
                        </div>
                    )}
                </div>
            </div>

            <div className="bottom_section">
                {!inputs.classId && (
                    <div
                        style={{
                            fontWeight: "bold",
                        }}
                    >
                         Select a class to create new courses
                    </div>
                )}

                <div
                    style={{
                        padding: "10px",
                        fontWeight: "bold",
                    }}
                >
                     Showing your courses
                </div>

                <div className="classes_container">
                    {enabledSubjects.map((subject) => {
                        return (
                            <div
                                className="class_tile"
                                key={subject.subject.id}
                                onClick={() => {
                                    takeToChapterPage(subject.subject.id);
                                }}
                            >
                                <div className="class_tile_image">
                                    {/* <img
											className="class_image"
											src={`${MAIN_API_URL}/${subject.subject.subject_cover.file_path}`}
											alt=""
										/> */}

                                    <img
                                        className="class_image"
                                        src={
                                            MAT_URL +
                                            subject.subject.subject_cover
                                                .file_path
                                        }
                                        alt="subject cover"
                                    />
                                    <span className="class_tile_title">
                                        {subject.subject.subject_name}
                                    </span>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>

            {createCoursePopUpOpen && (
                <CreateCoursePopUp
                    className="modal fade"
                    id={idMessage}
                    reload={reload}
                    setReload={setReload}
                    setCreateCoursePopUpOpen={setCreateCoursePopUpOpen}
                    classId={inputs.classId}
                    subjects={availableSubjects}
                    token={user.token}
                    tabIndex="-1"
                    role="dialog"
                    aria-labelledby="exampleModalLabel"
                    aria-hidden="true"
                />
            )}
        </div>
    );
}

export default ClassPage;
