import React from "react";
import { Link } from "react-router-dom";
import "./routineElement.scss";

const RoutineElement = ({ time, routine, courses }) => {
	return (
		<div className="routineRows">
			<div className="routineColsIndex">{time}</div>

			{routine.map((routine) => (
				<div key={routine.day} className="routineCols">
					{routine.course_id && (
						<Link
							to={`/course/${
								courses.find(
									(course) => course.id === routine.course_id
								).subject?.id
							}`}
							className="noDecoration"
							style={{
								color: "inherit",
							}}
						>
							{courses.find(
								(course) => course.id === routine.course_id
							).subject?.class.class_name +
								" - " +
								courses.find(
									(course) => course.id === routine.course_id
								).subject?.subject_name}
						</Link>
					)}
				</div>
			))}
		</div>
	);
};

export default RoutineElement;
