import React, { useEffect, useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import { useNavigate } from "react-router-dom";
import "./../popUp.scss";
import ModButton from "../../../components/ModButton/ModButton";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { getAllCLasses, deleteClass } from "../../../../../api/class.api";

function DeleteClassPopUp(props) {
	const { setDeleteClassPopUpOpen, setClasses, classItem } = props;

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		console.log(classItem);
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const handleDelete = () => {
		deleteClass({
			token: user.token,
			classId: classItem.id,
		})
			.then((res) => {
				console.log(res);
				getAllCLasses({ token: user.token })
					.then((res) => {
						setClasses(res.data.data);
					})
					.catch((err) => {
						console.log(err);
					});
				setDeleteClassPopUpOpen(false);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the delete class pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setDeleteClassPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>

			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<p>
						Do you want to delete{" "}
						<span>
							<b>{classItem.class_name}</b>
						</span>
						?
					</p>
				</div>

				<div className="ModeratorFormBtns">
					<ModButton btnText={"Delete"} onClick={handleDelete} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setDeleteClassPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default DeleteClassPopUp;
