import React, { useState } from "react";
import "./ShowMaterials.scss";
import axios from "axios";
import { MAT_URL } from "../../../../utils/urls";
import fileDownload from "js-file-download";
import { CircularProgressbar } from "react-circular-progressbar";
import {
	CloudDownloadOutlined,
	Delete,
	CheckCircleOutline,
} from "@material-ui/icons";
import { deleteMaterial } from "../../../../api/chapter.api";
import { getTeachersChapterInfoByChapterId } from "../../../../api/course.api";

export default function ShowMaterials({
	material,
	setChapterData,
	setCourseData,
	setMaterials,
	setIsLoading,
	token,
}) {
	const [downloadPercentage, setDownloadPercentage] = useState();
	const newDate = new Date(material.updated_at);
	const uploadedDate = newDate.toLocaleDateString();

	const handleMaterialDelete = (e, materialId, chapterId) => {
		e.preventDefault();
		setIsLoading(true);

		console.log(materialId);

		deleteMaterial({ materialId, token })
			.then(function (res) {
				getTeachersChapterInfoByChapterId({
					chapterId,
					token,
				})
					.then((res) => {
						setChapterData(res.data.data.chapterData);
						setCourseData(res.data.data.courseData);
						setMaterials(res.data.data.chapterMaterials);
						setIsLoading(false);
					})
					.catch((err) => {
						console.log(err);
						setIsLoading(false);
					});
			})
			.catch(function (error) {
				console.log(error);
				setIsLoading(false);
			});
	};

	const handleDownloadRequest = async (filePath, filename) => {
		var fullPath = MAT_URL + filePath;

		axios({
			method: "get",
			url: fullPath,
			responseType: "blob",
			onDownloadProgress: (progressEvent) => {
				let percentCompleted = Math.round(
					(progressEvent.loaded * 100) / progressEvent.total
				);
				setDownloadPercentage(percentCompleted);
			},
		})
			.then((res) => {
				console.log(res);
				fileDownload(res.data, filename);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const bytsToMB = (byts) => {
		return (byts / 1024 / 1024).toFixed(2);
	};

	const formatDateTime = (dueDateTime) => {
		// format => 14 July 2022 7:00 PM
		const date = new Date(dueDateTime);
		const day = date.getDate();
		const month = date.getMonth() + 1;
		const monthList = [
			"Jan",
			"Feb",
			"Mar",
			"Apr",
			"May",
			"Jun",
			"July",
			"Aug",
			"Sep",
			"Oct",
			"Nov",
			"Dec",
		];
		const textMonth = monthList[date.getMonth()];
		const year = date.getFullYear();
		return `${day} ${textMonth}, ${year}`;
	};

	return (
		<div className="material_container">
			<div className="material_container_thumbnail">
				<img
					src="https://cdn-icons-png.flaticon.com/512/747/747062.png"
					alt="thumbnail"
					className="material_container_thumbnail_img"
				/>
			</div>
			<div className="material_container_middle">
				<div className="material_name">
					File Name :{" "}
					{
						material.materials.file_path
							.split("/")
							[
								material.materials.file_path.split("/").length -
									1
							].split("--")[1]
					}
				</div>
				<div className="material_type">
					File Type : {material.materials.file_type}
				</div>
				<div className="material_size">
					File Size : {bytsToMB(material.materials.size)}MB
				</div>
				<div className="material_updateDate">
					Uploaded on : {formatDateTime(uploadedDate)}
				</div>
			</div>
			<div className="material_container_middle_right">
				{material.materials.uploder.role.is_admin && (
					<CheckCircleOutline
						style={{
							color: "green",
							fontSize: "2rem",
						}}
					/>
				)}

				<div>
					{material.materials.uploder.role.is_admin
						? "TSC Material"
						: "Teacher's Material"}
				</div>
			</div>
			<div className="material_container_right">
				{!material.materials.uploder.role.is_admin && (
					<div
						style={{
							display: "flex",
							flexDirection: "row",
							justifyContent: "space-between",
							alignItems: "center",
							padding: "5px",
							borderRadius: "5px",
							backgroundColor: "#DC143C",
							margin: "5px",
						}}
						onClick={(e) => {
							handleMaterialDelete(
								e,
								material.id,
								material.chapter_x_subject_id
							);
						}}
					>
						<div className="iconHolder">Delete</div>{" "}
						<Delete className="icon" />
					</div>
				)}

				{bytsToMB(material.materials.size) > 0 ? (
					<div>
						{!downloadPercentage ? (
							<div
								style={{
									display: "flex",
									flexDirection: "row",
									justifyContent: "space-between",
									alignItems: "center",
									padding: "5px",
									borderRadius: "5px",
									backgroundColor: "#46178f",
									margin: "5px",
								}}
								onClick={() => {
									handleDownloadRequest(
										material.materials.file_path,
										material.materials.file_path
											.split("/")
											[
												material.materials.file_path.split(
													"/"
												).length - 1
											].split("--")[1]
									);
								}}
							>
								<div className="iconHolder">Download</div>{" "}
								<CloudDownloadOutlined className="icon" />
							</div>
						) : (
							<div
								style={{
									width: 40,
									height: 40,
								}}
								onClick={() => {
									setDownloadPercentage(0);
								}}
							>
								<CircularProgressbar
									value={downloadPercentage}
									text={`${downloadPercentage}%`}
								/>
							</div>
						)}
					</div>
				) : (
					<div
						style={{
							color: "red",
							fontSize: "12px",
							paddingTop: "5px",
						}}
					>
						Download Not Available{" "}
					</div>
				)}
			</div>
		</div>
	);
}
