import React, { useEffect, useState } from "react";
import TimePicker from "react-time-picker";
import Loading from "../../../../../components/loading/Loading";
import { Add } from "@material-ui/icons";
import "./updateRoutine.scss";
import UpdateRoutineElement from "./../updateRoutineElement/UpdateRoutineElement";
import {
	createRoutineItem,
	deleteRoutineItem,
	getFullRoutine,
} from "../../../../../api/routine.api";

const UpdateRoutine = ({ user, editRoutine, setEditRoutine }) => {
	const [addNewRow, setAddNewRow] = useState(false);
	const [value, onChangeValue] = useState("10:00");
	const DayName = new Date().toLocaleDateString("en-US", {
		weekday: "short",
	});
	const [Routine, setRoutine] = useState([]);
	const [courses, setCourses] = useState([]);
	const [isLoadingRoutine, setIsLoadingRoutine] = useState(true);

	useEffect(() => {
		getRoutine();
	}, [editRoutine]);

	const getRoutine = () => {
		setIsLoadingRoutine(true);
		getFullRoutine({ userId: user.id, token: user.token })
			.then((res) => {
				setRoutine(res.data.data["routine_items"]);
				setCourses(res.data.data["courses"]);
				setIsLoadingRoutine(false);
			})
			.catch((err) => {
				console.log(err);
				setIsLoadingRoutine(false);
			});
	};

	const createNewRoutineItem = () => {
		createRoutineItem({
			token: user.token,
			userId: user.id,
			time: value,
		})
			.then((res) => {
				getRoutine();
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const deleteRoutine = (time) => {
		deleteRoutineItem({
			token: user.token,
			userId: user.id,
			time: time,
		})
			.then((res) => {
				getRoutine();
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className="SelectRoutine">
			<div
				className={`${
					!editRoutine
						? "SelectRoutineFadeOut"
						: "SelectRoutineFadeIn"
				}`}
			>
				<div className="RoutineMakerContainer">
					<div
						className="routineRows"
						style={{
							backgroundColor: "#006edc",
							color: "white",
						}}
					>
						<div className="routineColsIndex">Time</div>
						<div
							className={
								DayName === "Sun"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Sun
						</div>
						<div
							className={
								DayName === "Mon"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Mon
						</div>
						<div
							className={
								DayName === "Tue"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Tue
						</div>
						<div
							className={
								DayName === "Wed"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Wed
						</div>
						<div
							className={
								DayName === "Thu"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Thu
						</div>
						<div
							className={
								DayName === "Fri"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Fri
						</div>
						<div
							className={
								DayName === "Sat"
									? "routineCols selectedDay"
									: "routineCols"
							}
						>
							Sat
						</div>
					</div>
					{isLoadingRoutine ? (
						<div className="loading">
							<Loading />
							<h1>Loading Routine...</h1>
						</div>
					) : (
						<>
							{Routine.map((routine) => (
								<UpdateRoutineElement
									key={routine.time}
									time={routine.time}
									routine={routine.routine}
									editRoutine={editRoutine}
									deleteRoutine={deleteRoutine}
								/>
							))}
						</>
					)}
					{editRoutine && (
						<div className="Options">
							<div className="Options_left">
								<Add
									style={{
										borderRadius: "50%",
										padding: "10px",
										margin: "10px",
										marginLeft: "10px",
										cursor: "pointer",
										border: "1px solid #fff",
										backgroundColor: addNewRow
											? "#fff"
											: "#00bcd4",
										color: addNewRow ? "#00bcd4" : "#fff",
									}}
									onClick={() => {
										setAddNewRow(!addNewRow);
									}}
								/>
								{addNewRow && (
									<>
										<TimePicker
											onChange={onChangeValue}
											value={value}
											format={"hh:mm a"}
										/>

										<button
											className="btn-primary"
											onClick={createNewRoutineItem}
										>
											Create New
										</button>
									</>
								)}
							</div>
							<div className="Options_right">
								<button
									onClick={() => setEditRoutine(!editRoutine)}
								>
									DONE
								</button>
							</div>
						</div>
					)}
				</div>
			</div>
		</div>
	);
};

export default UpdateRoutine;
