import React, { useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import "./../popUp.scss";
import { getModeratorCourseInfo } from "../../../../../api/course.api";
import ModButton from "../../ModButton/ModButton";
import { CREATE_CHAPTER } from "../../../../../utils/urls";
import axios from "axios";

function CreateChapterPopUp(props) {
	const {
		setCreateChapterPopUpOpen,
		classId,
		subjectId,
		token,
		setChapters,
	} = props;

	const [selectedFile, setSelectedFile] = useState({
		chapter_name: "",
		chapter_serial: "",
		class_id: classId,
		subject_x_class_id: subjectId,
	});

	const [chapter_Icon, setChapterIcon] = useState();
	const [chapter_Cover, setChapterCover] = useState();
	const [formError, setFormError] = useState(null);

	const handleChange = (e) => {
		setSelectedFile({
			...selectedFile,
			[e.target.name]: e.target.value,
		});
		console.log(selectedFile.chapter_name);
	};

	// Create Chapters
	const handleSubmit = () => {
		var formData = new FormData();
		formData.append("chapter_name", selectedFile.chapter_name);
		formData.append("chapter_serial", selectedFile.chapter_serial);
		formData.append("class_id", selectedFile.class_id);
		formData.append("subject_x_class_id", selectedFile.subject_x_class_id);
		formData.append("chapter_icon", chapter_Icon);
		formData.append("chapter_cover", chapter_Cover);
		if (
			selectedFile.chapter_name === "" ||
			selectedFile.chapter_serial === ""
		) {
			setFormError("Chapter name and serial are required");
		} else if (chapter_Icon === undefined || chapter_Cover === undefined) {
			setFormError("Chapter Icon and Cover are required");
		} else {
			axios({
				method: "post",
				url: CREATE_CHAPTER,
				headers: {
					"x-auth-token": token,
					"Content-Type": "multipart/form-data",
				},
				data: formData,
			})
				.then((res) => {
					setCreateChapterPopUpOpen(false);
					getModeratorCourseInfo({ subjectId, token })
						.then((res) => {
							setChapters(res.data.data.chapters);
						})
						.catch((err) => {
							console.log(err);
						});
				})
				.catch((err) => {
					console.log(err);
				});
		}
	};

	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the create chapter pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setCreateChapterPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>
			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<div className="form_field">
						<label>Chapter Name</label>
						<input
							onChange={handleChange}
							type="text"
							placeholder="Chapter Name"
							name="chapter_name"
							required
							className="ModeratorFormInput"
						/>
					</div>

					<div className="form_field">
						<label>Chapter Serial</label>
						<input
							type="text"
							placeholder="Chapter Serial"
							name="chapter_serial"
							required
							onChange={handleChange}
							className="ModeratorFormInput"
						/>
					</div>
				</div>

				<div className="form_row">
					<div className="form_field">
						<label>Class</label>
						<input
							type="text"
							placeholder="Class"
							value={classId}
							disabled
							required
							className="ModeratorFormInput"
						/>
					</div>

					<div className="form_field">
						<label>Subject</label>
						<input
							type="text"
							placeholder="Subject"
							value={subjectId}
							disabled
							required
							className="ModeratorFormInput"
						/>
					</div>
				</div>

				<div className="form_field">
					<label>Chapter Icon</label>
					<input
						name="chapter_icon"
						onChange={(e) => {
							setChapterIcon(e.target.files[0]);
						}}
						type="file"
						accept="image/png, image/gif, image/jpeg"
						required
						className="ModeratorFormInput"
					/>
				</div>

				<div className="form_field">
					<label>Chapter Cover</label>
					<input
						onChange={(e) => {
							setChapterCover(e.target.files[0]);
						}}
						type="file"
						name="chapter_cover"
						accept="image/png, image/gif, image/jpeg"
						required
						className="ModeratorFormInput"
					/>
				</div>

				{formError ? <p className="error">{formError}</p> : null}

				<div className="ModeratorFormBtns">
					<ModButton btnText="Create" onClick={handleSubmit} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setCreateChapterPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default CreateChapterPopUp;
