import { ArrowLeftTwoTone, PersonAddRounded } from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import Header from "../../components/header/Header";
import "./chapterPage.scss";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import { getTeacherCourseInfoBySubjectId } from "../../../../api/course.api";
import { MAT_URL } from "../../../../utils/urls";
import { getClassSchedule } from "../../../../api/routine.api";

function ChapterPage() {
	const user = loadStorage("user");
	const navigate = useNavigate();
	const subjectId = window.location.href.split("/")[4];
	const [isLoading, setIsLoading] = useState(true);
	const [courseData, setCourseData] = useState({});
	const [chapters, setChapters] = useState([]);
	const [classData, setClassData] = useState({});
	const [schedule, setSchedule] = useState([]);

	useEffect(() => {
		if (!user) {
			navigate("/auth/login");
		} else {
			getTeacherCourseInfoBySubjectId({ subjectId, token: user.token })
				.then((res) => {
					// console.log("Course Data", res.data.data.courseData);
					// console.log("Chapters", res.data.data.chapters);
					// console.log("Class Data", res.data.data.classData);
					setCourseData(res.data.data.courseData);
					setChapters(res.data.data.chapters);
					setClassData(res.data.data.classData);

					getClassSchedule({
						userId: user.id,
						courseId: res.data.data.courseData.id,
						token: user.token,
					})
						.then((res) => {
							console.log(res.data.data);
							setSchedule(res.data.data);
						})
						.catch((err) => {
							console.log(err);
						});
					setIsLoading(false);
				})
				.catch((err) => {
					console.log(err);
					setIsLoading(false);
				});
		}
	}, []);

	// const location = useLocation();
	// const { prevPage } = location.state;

	// console.log("prevPage", prevPage);

	function nextDate(date) {
		let nextDate = new Date(date);
		nextDate.setDate(nextDate.getDate() + 1);
		return nextDate;
	}

	const [next7Days, setNext7Days] = useState([
		new Date(),
		nextDate(new Date()),
		nextDate(nextDate(new Date())),
		nextDate(nextDate(nextDate(new Date()))),
		nextDate(nextDate(nextDate(nextDate(new Date())))),
		nextDate(nextDate(nextDate(nextDate(nextDate(new Date()))))),
		nextDate(nextDate(nextDate(nextDate(nextDate(nextDate(new Date())))))),
	]);

	return (
		<div
			style={{
				width: "100vw",
				display: "flex",
				flexDirection: "column",

				alignItems: "center",
			}}
		>
			<div
				style={{
					width: "100%",
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
					marginBottom: "20px",
					boxShadow: "0px 0px 10px #00000029",
				}}
			>
				<Header />
			</div>

			{isLoading ? (
				<div>
					<p
						style={{
							textAlign: "center",
							marginTop: "20px",
							marginBottom: "20px",
							fontWeight: "bold",
							fontSize: "20px",
						}}
					>
						Loading...
					</p>
				</div>
			) : (
				<>
					<div className="main">
						<div className="right-side">
							<div
								className="division_2"
								style={{ background: "white" }}
							>
								<div
									className="path"
									onClick={() =>
										navigate(-1, {
											state: {
												prevPage: "classes",
											},
										})
									}
								>
									<ArrowLeftTwoTone className="Icon" /> Go
									back to Dashboard
								</div>

								<div className="mid_row">
									<div className="mid_row_top">
										<div className="mid_row_left">
											<div className="mid_row_left_top">
												<div className="user_information">
													<div className="user_pic">
														<img
															className="user_pic_img"
															src={
																user.profile_picture
															}
															alt=""
														/>
													</div>
													<div className="name_info">
														<span className="name_text">
															{user.first_name +
																" " +
																user.last_name}
														</span>
													</div>
												</div>
												<div className="start_class">
													<Link
														to="/"
														className="start_class_text"
													>
														Start Class
													</Link>
												</div>
											</div>

											<div className="course_details">
												<div className="course_details_tiles_left">
													{classData.class_name} -{" "}
													{
														courseData.subject
															.subject_name
													}
												</div>

												<div className="course_details_tiles_right">
													<div className="rating_text">
														A
													</div>
												</div>
											</div>
										</div>

										<div className="mid_row_right">
											Class Schedule
											<table className="date_table">
												<thead>
													<tr>
														<th className="tg-0lax">
															Weekday
														</th>
														<th className="tg-0lax">
															Time
														</th>
													</tr>
												</thead>
												<tbody className="date_table">
													{schedule.map(
														(item, index) => {
															return (
																<tr key={index}>
																	<td className="tg-0lax">
																		{
																			item.day
																		}
																	</td>
																	<td className="tg-0lax">
																		{
																			item.time
																		}
																	</td>
																</tr>
															);
														}
													)}
													{/* <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr>
                                                    <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr>
                                                    <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr>
                                                    <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr>
                                                    <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr>
                                                    <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr>
                                                    <tr>
                                                        <td className="tg-0lax"></td>
                                                        <td className="tg-0lax"></td>
                                                    </tr> */}
												</tbody>
											</table>
										</div>
									</div>
								</div>
							</div>

							<div className="chapter">Chapters</div>
							<div className="bottom_section">
								<div className="chapter_info">
									<div className="chapter_list_container">
										<div
											className="chapter_card"
											style={{
												background: "grey",
											}}
										>
											<div className="headings">
												Chapter Icon
											</div>

											<div className="headings">
												Chapter Name
											</div>
											<div className="headings">
												Chapter Serial
											</div>

											<div className="headings">
												Chapter Options
											</div>
										</div>
										{chapters.map((chapter) => (
											<div
												className="chapter_card"
												key={chapter.id}
											>
												{/* <div>
                                                    {JSON.stringify(chapter)}
                                                </div> */}

												<div className="headings">
													<img
														className="icon_img"
														src={
															MAT_URL +
																chapter
																	.chapter_icon
																	.file_path ||
															""
														}
														alt="chapter icon"
													/>
												</div>
												<div className="headings">
													<span className="chpater_name_text">
														{chapter.chapter_name}
													</span>
												</div>
												<div className="headings">
													<span className="chpater_title_text">
														Chapter{" "}
														{chapter.chapter_serial}
													</span>
												</div>

												<div className="open_chapter">
													<Link
														to={`/chapter/${chapter.id}`}
														className="open_chapter_text"
													>
														Open Chapter
													</Link>
												</div>
											</div>
										))}
									</div>
								</div>
								{/* <div
                                    className="student_info"
                                    style={{
                                        fontSize: "1rem",
                                        color: "#fff",
                                    }}
                                >
                                    <div className="student_info_title">
                                        <span className="student_info_title_text">
                                            Students
                                        </span>
                                        <div className="student_info_title_icon">
                                            <PersonAddRounded
                                                style={{
                                                    fontSize: "2rem",
                                                    color: "#fff",
                                                }}
                                            />
                                        </div>
                                    </div>
                                    No students added
                                </div> */}
							</div>
						</div>
					</div>
				</>
			)}
		</div>
	);
}

export default ChapterPage;
