import React, { useEffect, useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import { useNavigate } from "react-router-dom";
import "./../popUp.scss";
import ModButton from "../../../components/ModButton/ModButton";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { getAllCLasses, updateClass } from "../../../../../api/class.api";

function UpdateClassPopUp(props) {
	const { setUpdateClassPopUpOpen, setClasses, classItem } = props;

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const [inputs, setInputs] = useState({
		class_id: classItem.id,
		class_name: classItem.class_name,
	});
	const [classNameError, setClassNameError] = useState("");

	const handleInputChange = (e) => {
		const name = e.target.name;
		const value = e.target.value;

		setInputs({ ...inputs, [name]: value });
	};

	const handleUpdate = () => {
		console.log("update class");
		console.log(inputs);

		if (inputs.class_name === "") {
			setClassNameError("Class name is required");
		} else {
			setClassNameError("");
			updateClass({
				token: user.token,
				classId: inputs.class_id,
				class_name: inputs.class_name,
			})
				.then((res) => {
					console.log(res);
					getAllCLasses({ token: user.token })
						.then((res) => {
							setClasses(res.data.data);
						})
						.catch((err) => {
							console.log(err);
						});
					setUpdateClassPopUpOpen(false);
				})
				.catch((err) => {
					console.log(err);
				});
		}
	};

	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the create course pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setUpdateClassPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>
			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<div className="form_field">
						<label>Class Name</label>
						<input
							type="text"
							name="class_name"
							placeholder="Class Name"
							required
							autoFocus
							className="ModeratorFormInput"
							value={inputs.class_name}
							onChange={handleInputChange}
						/>
						<span className="error">{classNameError}</span>
					</div>
				</div>

				<div className="ModeratorFormBtns">
					<ModButton btnText={"Update"} onClick={handleUpdate} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setUpdateClassPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default UpdateClassPopUp;
