import { Close, InfoOutlined } from "@material-ui/icons";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { getModeratorCourseInfo } from "../../../../../api/course.api";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { UPDATE_CHAPTER_URL } from "../../../../../utils/urls";
import ModButton from "../../ModButton/ModButton";

function UpdateChapterPopUp(props) {
	const {
		setUpdateChapterPopUpOpen,
		classId,
		subjectId,
		token,
		setChapters,
		chapterItem,
	} = props;

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const [inputs, setInputs] = useState({
		chapter_id: chapterItem.id,
		chapter_name: chapterItem.chapter_name,
		chapter_serial: chapterItem.chapter_serial,
		class_id: classId,
		subject_id: subjectId,
	});
	const [chapterIcon, setChapterIcon] = useState();
	const [chapterCover, setChapterCover] = useState();
	const [chapterError, setChapterError] = useState("");

	const handleChange = (e) => {
		const name = e.target.name;
		const value = e.target.value;

		setInputs({ ...inputs, [name]: value });
	};

	const handleUpdate = () => {
		console.log("update chapter");
		console.log(inputs);
		console.log(chapterIcon);
		console.log(chapterCover);

		if (inputs.chapter_name === "") {
			setChapterError("Chapter name is required");
			return;
		} else if (inputs.chapter_serial === "") {
			setChapterError("Chapter serial is required");
			return;
		} else {
			setChapterError("");
			const formData = new FormData();
			formData.append("chapter_id", inputs.chapter_id);
			formData.append("chapter_name", inputs.chapter_name);
			formData.append("chapter_serial", inputs.chapter_serial);
			formData.append("class_id", inputs.class_id);
			formData.append("chapter_icon", chapterIcon);
			formData.append("chapter_cover", chapterCover);

			if (chapterIcon === undefined) {
				setChapterError("Chapter icon is required");
				return;
			}
			if (chapterCover === undefined) {
				setChapterError("Chapter cover is required");
				return;
			}

			console.log(inputs);

			axios({
				method: "patch",
				url: UPDATE_CHAPTER_URL + "/" + inputs.chapter_id,
				headers: {
					"Content-Type": "multipart/form-data",
					"x-auth-token": user.token,
				},
				data: formData,
			})
				.then((res) => {
					console.log(res);
					setUpdateChapterPopUpOpen(false);
					getModeratorCourseInfo({
						subjectId,
						token,
					})
						.then((res) => {
							console.log(res.data.data);
							setChapters(res.data.data.chapters);
						})
						.catch((err) => {
							console.log(err);
						});
				})
				.catch((err) => {
					console.log(err);
				});
		}
	};

	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the create course pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setUpdateChapterPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>
			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<div className="form_field">
						<label>Chapter Name</label>
						<input
							type="text"
							placeholder="Chapter Name"
							name="chapter_name"
							required
							value={inputs.chapter_name}
							onChange={handleChange}
							className="ModeratorFormInput"
						/>
					</div>

					<div className="form_field">
						<label>Chapter Serial</label>
						<input
							type="text"
							placeholder="Chapter Serial"
							name="chapter_serial"
							required
							value={inputs.chapter_serial}
							onChange={handleChange}
							className="ModeratorFormInput"
						/>
					</div>
				</div>

				<div className="form_row">
					<div className="form_field">
						<label>Class</label>
						<input
							type="text"
							placeholder="Class"
							value={classId}
							disabled
							required
							className="ModeratorFormInput"
						/>
					</div>

					<div className="form_field">
						<label>Subject</label>
						<input
							type="text"
							placeholder="Subject"
							value={subjectId}
							disabled
							required
							className="ModeratorFormInput"
						/>
					</div>
				</div>

				<div className="form_field">
					<label>Chapter Icon</label>
					<input
						name="chapter_icon"
						onChange={(e) => {
							setChapterIcon(e.target.files[0]);
						}}
						type="file"
						accept="image/png, image/gif, image/jpeg"
						required
						className="ModeratorFormInput"
					/>
				</div>

				<div className="form_field">
					<label>Chapter Cover</label>
					<input
						onChange={(e) => {
							setChapterCover(e.target.files[0]);
						}}
						type="file"
						name="chapter_cover"
						accept="image/png, image/gif, image/jpeg"
						required
						className="ModeratorFormInput"
					/>
				</div>

				{chapterError ? <p className="error">{chapterError}</p> : null}

				<div className="ModeratorFormBtns">
					<ModButton btnText={"Update"} onClick={handleUpdate} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setUpdateChapterPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default UpdateChapterPopUp;
