import React, { useEffect, useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import { useNavigate } from "react-router-dom";
import "./../popUp.scss";
import ModButton from "../../../components/ModButton/ModButton";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { UPDATE_SUBJECT } from "../../../../../utils/urls";
import axios from "axios";
import { getEnabledSubjectsByClassID } from "../../../../../api/subject.api";

function UpdateSubjectPopUp(props) {
	const { setUpdateSubjectPopUpOpen, setSubjects, subjectItem } = props;
	console.log(subjectItem);

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const [inputs, setInputs] = useState({
		subject_id: subjectItem.id,
		subject_name: subjectItem.subject_name,
	});
	const [subjectIcon, setSubjectIcon] = useState();
	const [subjectCover, setSubjectCover] = useState();
	const [subjectNameError, setSubjectNameError] = useState("");

	const handleInputChange = (e) => {
		const name = e.target.name;
		const value = e.target.value;

		setInputs({ ...inputs, [name]: value });
	};

	const iconChnageHandler = (event) => {
		setSubjectIcon(event.target.files[0]);
	};

	const coverChangeHandler = (event) => {
		setSubjectCover(event.target.files[0]);
	};

	const handleUpdate = () => {
		console.log("update subject");
		console.log(inputs);
		console.log(subjectIcon);
		console.log(subjectCover);

		if (inputs.subject_name === "") {
			setSubjectNameError("Subject name is required");
		} else if (subjectIcon === undefined || subjectCover === undefined) {
			setSubjectNameError("Subject icon and cover is required");
		} else {
			setSubjectNameError("");
			const formData = new FormData();
			formData.append("subject_id", inputs.subject_id);
			formData.append("subject_name", inputs.subject_name);
			formData.append("subject_icon", subjectIcon);
			formData.append("subject_cover", subjectCover);

			axios({
				method: "patch",
				url: UPDATE_SUBJECT + "/" + inputs.subject_id,
				headers: {
					"Content-Type": "multipart/form-data",
					"x-auth-token": user.token,
				},
				data: formData,
			})
				.then((res) => {
					console.log(res);
					setUpdateSubjectPopUpOpen(false);
					getEnabledSubjectsByClassID({
						classId: subjectItem.class_id,
						token: user.token,
					})
						.then((res) => {
							console.log(res.data.data);
							setSubjects(res.data.data);
						})
						.catch((err) => {
							console.log(err);
						});
				})
				.catch((err) => {
					console.log(err);
				});
		}
	};

	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the update subject pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setUpdateSubjectPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>
			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<div className="form_field">
						<label>Subject Name</label>
						<input
							type="text"
							name="subject_name"
							placeholder="Subject Name"
							required
							autoFocus
							className="ModeratorFormInput"
							value={inputs.subject_name}
							onChange={handleInputChange}
						/>
					</div>
				</div>
				<div className="form_row">
					<div className="form_field">
						<label>Subject Icon</label>
						<input
							type="file"
							accept="image/png, image/gif, image/jpeg"
							name="subject_icon"
							required
							className="ModeratorFormInput"
							onChange={iconChnageHandler}
						/>
					</div>
				</div>
				<div className="form_row">
					<div className="form_field">
						<label>Subject Cover</label>
						<input
							type="file"
							accept="image/png, image/gif, image/jpeg"
							name="subject_cover"
							required
							className="ModeratorFormInput"
							onChange={coverChangeHandler}
						/>
					</div>
				</div>
				<span className="error">{subjectNameError}</span>

				<div className="ModeratorFormBtns">
					<ModButton btnText={"Update"} onClick={handleUpdate} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setUpdateSubjectPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default UpdateSubjectPopUp;
