import React, { useEffect, useState } from "react";
import RoutineElement from "../routineElement/RoutineElement";
import "./routineMaker.scss";
import { getFullRoutine } from "../../../../../api/routine.api";
import Loading from "../../../../../components/loading/Loading";

export const RoutineMaker = ({ user, editRoutine }) => {
	const DayName = new Date().toLocaleDateString("en-US", {
		weekday: "short",
	});
	const [routine, setRoutine] = useState([]);
	const [courses, setCourses] = useState([]);
	const [isLoadingRoutine, setIsLoadingRoutine] = useState(true);

	useEffect(() => {
		getRoutine();
	}, [editRoutine]);

	const getRoutine = () => {
		setIsLoadingRoutine(true);
		getFullRoutine({ userId: user.id, token: user.token })
			.then((res) => {
				setRoutine(res.data.data["routine_items"]);
				setCourses(res.data.data["courses"]);
				setIsLoadingRoutine(false);
			})
			.catch((err) => {
				console.log(err);
				setIsLoadingRoutine(false);
			});
	};

	return (
		<div className="RoutineMakerContainer">
			<div
				className="routineRows"
				style={{
					backgroundColor: "#006edc",
					color: "white",
				}}
			>
				<div className="routineColsIndex">Time</div>
				<div
					className={
						DayName === "Sun"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Sun
				</div>
				<div
					className={
						DayName === "Mon"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Mon
				</div>
				<div
					className={
						DayName === "Tue"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Tue
				</div>
				<div
					className={
						DayName === "Wed"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Wed
				</div>
				<div
					className={
						DayName === "Thu"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Thu
				</div>
				<div
					className={
						DayName === "Fri"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Fri
				</div>
				<div
					className={
						DayName === "Sat"
							? "routineCols selectedDay"
							: "routineCols"
					}
				>
					Sat
				</div>
			</div>

			{isLoadingRoutine ? (
				<div className="loading">
					<Loading />
				</div>
			) : (
				<>
					{routine.map((item) => (
						<RoutineElement
							key={item.time}
							time={item.time}
							routine={item.routine}
							courses={courses}
						/>
					))}
				</>
			)}
		</div>
	);
};
