import axios from "axios";
import {
    GET_TEACHERS_COURSE_INFO_BY_SUBJECT_ID,
    GET_TEACHERS_CHAPTER_INFO_BY_CHAPTER_ID,
    GET_MODERATOR_CHAPTER_INFO_BY_CHAPTER_ID,
    GET_MODERATORS_COURSE_INFO_BY_SUBJECT_ID,
    CREATE_CHAPTER,
    CREATE_MATERIALS_URL,
    DELETE_CHAPTER_MATERIALS_URL,
    DELETE_CHAPTER_URL,
} from "../utils/urls";

export const getTeachersCourseInfoBySubjectId = (data) => {
    return axios({
        method: "get",
        url: GET_TEACHERS_COURSE_INFO_BY_SUBJECT_ID + `/${data.subjectId}`,
        headers: {
            "Content-Type": "application/json",
            "x-auth-token": data.token,
        },
    });
};

