import {
	EmailOutlined,
	Facebook,
	Group,
	Room,
	SchoolOutlined,
	StarBorderOutlined,
} from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { getUserDetails, updateUser } from "../../../api/user.api";
import { loadStorage, saveStorage } from "../../../utils/persistLocalStorage";
import Header from "../components/header/Header";
import EditProfilePopUp from "../components/popUps/editProfilePopUp/EditProfilePopUp";
import "./TeachersProfile.scss";

const TeachersProfile = () => {
	var user = loadStorage("user");
	const navigate = useNavigate();

	const [userDetails, setUserDetails] = useState({});
	const [courses, setCourses] = useState([]);
	const [editProfile, setEditProfile] = useState(false);
	const [isLoading, setIsLoading] = useState(true);

	useEffect(() => {
		user = loadStorage("user");
		if (!user) {
			navigate("/auth/login");
		} else {
			getUserDetails({ userId: user.id, token: user.token })
				.then((res) => {
					console.log(res.data.data);
					setUserDetails(res.data.data["user"]);
					setCourses(res.data.data["courses"]);
					setIsLoading(false);
				})
				.catch((err) => {
					console.log(err);
					setIsLoading(false);
				});
		}
	}, []);

	return (
		<div>
			{isLoading ? (
				<div className="loading-container">
					<div className="loading-spinner"></div>
				</div>
			) : (
				<>
					<div className="teachers_profile_container">
						<div className="teachers_profile_header_container">
							<Header />
						</div>
						<div className="teachers_profile_body_container">
							<div className="teachers_profile_body_left_container">
								<img
									className="teachers_profile_body_left_image"
									src={user.profile_picture}
									alt="img"
								/>

								<div className="teachers_profile_card">
									<div className="teachers_name">
										{userDetails &&
											userDetails.first_name +
												" " +
												userDetails.last_name}
									</div>
									<div className="teachers_email">
										Email:{" "}
										{userDetails && userDetails.email}
									</div>
									{userDetails.phone && (
										<div className="teachers_email">
											Phone:{" "}
											{userDetails && userDetails.phone}
										</div>
									)}

									{!editProfile && (
										<div
											className="Edit_profile_btn"
											onClick={() =>
												setEditProfile(!editProfile)
											}
										>
											Edit Your Profile
										</div>
									)}

									<div className="teachers_profile_card_info">
										<Group className="icon" />0 Students {" "}
										{userDetails.class_count} Classes
									</div>
									<div className="teachers_profile_card_edit_info">
										{userDetails.address && (
											<div
												style={{
													display: "flex",
													flexDirection: "row",
													justifyContent:
														"flex-start",
													alignItems: "flex-start",
												}}
											>
												<Room className="icon" />
												{userDetails.address}
											</div>
										)}
									</div>
									<span className="divider"></span>
									<div className="teachers_profile_card_rating">
										<StarBorderOutlined className="icon" />{" "}
										<span>0.0/5.0</span>
									</div>
									<span className="divider"></span>
									<div className="teachers_profile_card_orgs">
										<SchoolOutlined className="icon" />{" "}
										<span>No Organizations</span>
									</div>
								</div>
							</div>

							<div className="teachers_profile_body_right_container">
								<div className="about_teacher">
									<div className="about_teacher_header">
										 About me
									</div>
									<div className="about_teacher_body">
										Lorem ipsum dolor sit amet, consectetur
										adipiscing elit. Donec euismod, nisl
										eget consectetur consectetur, nisi nisl
										tincidunt nisi, eget consectetur nisi
										nisl eget consectetur consectetur.
									</div>
								</div>
								<hr />
								<div className="courses">
									<div className="courses_header">
										 Courses
									</div>
									<div className="courses_body">
										{JSON.stringify(courses)}
									</div>
								</div>
								<hr />
							</div>
						</div>
					</div>

					{editProfile && (
						<EditProfilePopUp
							setEditProfile={setEditProfile}
							userDetails={userDetails}
							setUserDetails={setUserDetails}
							user={user}
						/>
					)}
				</>
			)}
		</div>
	);
};

export default TeachersProfile;
