import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Close, InfoOutlined } from "@material-ui/icons";
import "../../popUps/popUp.scss";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { CREATE_MATERIALS_URL } from "../../../../../utils/urls";
import ProgressBar from "@ramonak/react-progress-bar";

function CreateMaterialPopUp(props) {
    const { setCreateMaterialsPopupOpen, chapter_x_subject_id, course_id } =
        props;
    // console.log(props);

    const navigate = useNavigate();
    var user = loadStorage("user");

    useEffect(() => {
        user = loadStorage("user");
        if (!user || !user.token) {
            navigate("/auth/login");
        }
    }, [user]);

    const [selectedFile1, setSelectedFile1] = useState();
    const [selectFileError, setSelectFileError] = useState("");
    const [uploadProgress, setUploadProgress] = useState(0);
    const [uploading, setUploading] = useState(false);

    const changeHandler1 = (event) => {
        setSelectedFile1(event.target.files[0]);
    };

    const uploadMaterial = () => {
        console.log("upload Material");
        if (!selectedFile1) {
            setSelectFileError("Please select a file");
            return;
        } else {
            setUploading(true);
            const config = {
                headers: {
                    "x-auth-token": user.token,
                    "Content-Type": "multipart/form-data",
                },
                onUploadProgress: function (progressEvent) {
                    console.log(progressEvent);

                    var percentCompleted = Math.round(
                        (progressEvent.loaded * 100) / progressEvent.total
                    );
                    console.log("Upload progress=>", percentCompleted);
                    setUploadProgress(percentCompleted);
                },
            };

            setSelectFileError("");
            const formData = new FormData();
            formData.append("chapter_x_subject_id", chapter_x_subject_id);
            formData.append("course_id", course_id);
            formData.append("chapter_material", selectedFile1);
            console.log(formData);

            axios
                .post(CREATE_MATERIALS_URL, formData, config)
                .then((res) => {
                    // alert("Materials upload compleated");
                    setCreateMaterialsPopupOpen(false);
                    // window.location.reload();
                })
                .catch((err) => {
                    console.log(err);
                });
        }
    };

    return (
        <div className="popup_container">
            <div className="action_row">
                <div className="action_row_left">
                    <InfoOutlined
                        onClick={() => {
                            alert("This is the create course pop up");
                        }}
                        style={{
                            fontSize: "2.5rem",
                            cursor: "pointer",
                        }}
                    />
                </div>
                <div className="action_row_right">
                    <Close
                        onClick={() => {
                            setCreateMaterialsPopupOpen(false);
                        }}
                        style={{
                            fontSize: "2.5rem",
                            cursor: "pointer",
                        }}
                    />
                </div>
            </div>

            <div className="popup_title">
                <h2>Upload Material</h2>
            </div>

            <div className="form">
                <div className="popup_choice_row">
                    <div className="popup_choice">
                        <input
                            type="file"
                            title="Material"
                            required
                            onChange={changeHandler1}
                        />
                    </div>
                </div>

                <div className="popup_error">
                    {setSelectFileError && <p>{selectFileError}</p>}
                </div>
            </div>
            {uploading && (
                <div className="progressRow">
                    <div
                        style={{
                            padding: "5px",
                        }}
                    >
                        File Upload in Progress
                    </div>
                    <ProgressBar
                        completed={uploadProgress}
                        labelAlignment="center"
                    />
                </div>
            )}

            <div className="popup_btn_row">
                <span className="popup_btn" onClick={uploadMaterial}>
                    Create
                </span>

                <span
                    className="popup_btn"
                    onClick={() => {
                        setCreateMaterialsPopupOpen(false);
                    }}
                >
                    Cancel
                </span>
            </div>
        </div>
    );
}

export default CreateMaterialPopUp;
