import React, { useEffect, useState } from "react";
import {
	createQuestion,
	deleteQuestion,
	updateFormInfo,
} from "../../api/exam.api";
import FormTitleDescription from "../form_components/form_title_desc/FormTitleDescription";
import QuestionCard from "../form_components/question_card/QuestionCard";
import "./formBody.scss";

function FormBody({ user, formData }) {
	console.log(formData);
	const [title, setTitle] = useState(formData.title);
	const [description, setDescription] = useState(formData.details);
	const formChapter = formData.chapter;
	const formCourse = formData.course;
	const [formDue, setFormDue] = useState(formData.due_date_time);
	const [formPassMark, setFormPassMark] = useState(formData.pass_marks);
	const [formTotalmarks, setFormTotalmarks] = useState(formData.total_marks);
	const [questions, setQuestions] = useState([]);

	const handleAddQuestion = () => {
		createQuestion({
			token: user.token,
			examId: formData.id,
		})
			.then((res) => {
				console.log(res);
				setQuestions([...questions, res.data.data]);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const handleDeleteQuestion = (questionId) => {
		deleteQuestion({ token: user.token, questionId: questionId })
			.then((res) => {
				console.log(res);
				setQuestions(
					questions.filter((question) => question.id !== questionId)
				);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	useEffect(() => {
		setQuestions(formData.questions.length > 0 ? formData.questions : []);
	}, [formData.questions]);

	const handleChangeInfo = (e) => {
		const { name, value } = e.target;
		if (name === "title") {
			setTitle(value);
		} else if (name === "description") {
			setDescription(value);
		} else if (name === "due_date_time") {
			setFormDue(value);
		} else if (name === "pass_marks") {
			setFormPassMark(value);
		} else if (name === "total_marks") {
			setFormTotalmarks(value);
		}
	};

	const handleUpdateInfo = () => {
		console.log(title, description, formDue, formPassMark);
		updateFormInfo({
			token: user.token,
			uuid: formData.uuid,
			title: title,
			description: description,
			dueDateTime: formDue,
			passMarks: formPassMark,
			totalMarks: formTotalmarks,
		})
			.then((res) => {
				console.log(res.data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className="form_body_container">
			<FormTitleDescription
				formTitle={title}
				formDescription={description}
				formChapter={formChapter}
				formCourse={formCourse}
				formDue={formDue}
				formPassMark={formPassMark}
				formTotalmarks={formTotalmarks}
				handleChange={handleChangeInfo}
				handleUpdateInfo={handleUpdateInfo}
			/>
			{questions &&
				questions.map((question, index) => {
					return (
						<QuestionCard
							key={index}
							question={question}
							index={index}
							chapterId={formChapter.id}
							courseId={formCourse.id}
							userId={user.id}
							token={user.token}
							examUUID={formData.uuid}
							handleDeleteQuestion={handleDeleteQuestion}
						/>
					);
				})}
			<div className="add_question_container">
				<button
					className="add_question_button"
					onClick={handleAddQuestion}
				>
					Add Question
				</button>
			</div>
		</div>
	);
}

export default FormBody;
