import axios from "axios";
import React, { useState } from "react";
import { loadStorage } from "../../utils/persistLocalStorage";
import { CREATE_REPORT_URL } from "../../utils/urls";
import "./devReportFormPopup.scss";

const DevReportFormPopup = ({ setCreateReport, setReload, reload }) => {
	const [post, setPost] = useState({
		topic: "",
		details: "",
	});
	const [error, setError] = useState();

	const handleOnCreate = () => {
		if (post.topic === "" || post.description === "") {
			setError("Please fill all the fields");
		} else {
			var user = loadStorage("user");
			var config = {
				method: "post",
				url: CREATE_REPORT_URL,
				headers: {
					"x-auth-token": user.token,
					"Content-Type": "application/json",
				},
				data: post,
			};

			axios(config)
				.then(function (res) {
					console.log(JSON.stringify(res.data));
					setReload(!reload);
					setCreateReport(false);
				})
				.catch(function (error) {
					console.log(error);
				});
		}
	};

	return (
		<div className="devReportFormPopupContainer">
			<div className="label">Topic</div>
			<input
				type="text"
				placeholder="Topic"
				className="topic_input_holder"
				onChange={(e) => {
					setPost({ ...post, topic: e.target.value });
				}}
			/>
			<div className="label">Details</div>
			<textarea
				placeholder="Details"
				className="details_input_holder"
				onChange={(e) => {
					setPost({ ...post, details: e.target.value });
				}}
			/>

			{error && (
				<div
					style={{
						color: "red",
						fontSize: "14px",
					}}
				>
					{error}
				</div>
			)}

			<div className="btn_container">
				<button className="submit_button" onClick={handleOnCreate}>
					Submit
				</button>
				<button
					className="submit_button"
					style={{
						backgroundColor: "#F4DE33",
						color: "#000",
					}}
					onClick={() => {
						setCreateReport(false);
					}}
				>
					Cancel
				</button>
			</div>
		</div>
	);
};

export default DevReportFormPopup;
