import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { CREATE_BOOKS_URL } from "../../../../../utils/urls";
import "./bookUploadMod.scss";

export const BookUploadMod = ({
	handleShowBookUploadFile,
	chapter_x_subject_id,
	course_id,
	user,
}) => {
	const [bookThumbnailImg, setBookThumbnailImg] = useState();
	const [uploadedFile, setUploadedFile] = useState();
	const [preview, setPreview] = useState();
	const [selectFileError, setSelectFileError] = useState("");

	useEffect(() => {
		if (!bookThumbnailImg) {
			setPreview(undefined);
			return;
		}

		const objectUrl = URL.createObjectURL(bookThumbnailImg);
		setPreview(objectUrl);

		// free memory when ever this component is unmounted
		return () => URL.revokeObjectURL(objectUrl);
	}, [bookThumbnailImg]);

	const changeHandler1 = (event) => {
		setBookThumbnailImg(event.target.files[0]);
	};

	const changeHandler2 = (event) => {
		setUploadedFile(event.target.files[0]);
	};

	const handleUpload = () => {
		if (!bookThumbnailImg) {
			setSelectFileError("Please select a thumbnail");
			return;
		} else if (!uploadedFile) {
			setSelectFileError("Please select a file");
			return;
		} else {
			setSelectFileError("");

			var formData = new FormData();
			formData.append("chapter_x_subject_id", chapter_x_subject_id);
			formData.append("course_id", course_id);
			formData.append("chapter_book_thumb", bookThumbnailImg);
			formData.append("chapter_book", uploadedFile);

			axios({
				method: "post",
				url: CREATE_BOOKS_URL,
				headers: {
					"x-auth-token": user.token,
					"Content-Type": "multipart/form-data",
				},
				data: formData,
			})
				.then((res) => {
					alert("Book upload completed");
					// setCreateBookPopupOpen(false);
					window.location.reload();
				})
				.catch((err) => {
					console.log(err);
				});
		}
	};

	return (
		<div className="BookUploadModContainer">
			<div className="popupCard">
				<span
					className="close"
					onClick={() => handleShowBookUploadFile()}
				>
					x
				</span>
				<div className="popupCardHeader">Upload a Book Here</div>
				<div className="popupCardBody">
					<div className="left">
						<div className="ThumbnailText">Book Thumbnail</div>
						{bookThumbnailImg && (
							<img className="previewImg" src={preview} />
						)}
						<div className="inputField">
							<input
								type="file"
								onChange={changeHandler1}
								accept="image/*"
							/>
						</div>
					</div>
					<div className="right">
						<div className="ThumbnailText">Upload Book</div>
						{uploadedFile && (
							<div className="UplodedFileInfo">
								<div> FIle Name : {uploadedFile.name} </div>
								<div>
									File Size :{" "}
									{(uploadedFile.size / 1024 / 1024).toFixed(
										2
									)}
									{"MB"}
								</div>
								<div>File Type : {uploadedFile.type} </div>
							</div>
						)}
						<div className="inputField">
							<input type="file" onChange={changeHandler2} />
						</div>
					</div>
				</div>
				{selectFileError && (
					<div className="errorText">{selectFileError}</div>
				)}
				<div className="popupCardFooter" onClick={handleUpload}>
					Upload Book
				</div>
			</div>
		</div>
	);
};
