import React, { useState, useEffect } from "react";
import "./moderatorClassPage.scss";
import { useNavigate } from "react-router-dom";
import { getAllCLasses } from "../../../../api/class.api";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import ModPageHeader from "../../components/ModPageHeader/ModPageHeader";
import ModClassListItem from "../../components/ModClassListItem/ModClassListItem";
import CreateClassPopUp from "../../components/popUps/classPopUps/CreateClassPopUp";
import UpdateClassPopUp from "../../components/popUps/classPopUps/UpdateClassPopUp";
import ModButton from "../../components/ModButton/ModButton";
import DeleteClassPopUp from "../../components/popUps/classPopUps/DeleteClassPopUp";

function ModeratorClassPage() {
	const user = loadStorage("user");
	const navigate = useNavigate();

	const idCreatePopUp = "create_class_popup";
	const idUpdatePopUp = "update_class_popup";
	const idDeletePopUp = "delete_class_popup";
	const [createClassPopUpOpen, setCreateClassPopUpOpen] = useState(false);
	const [updateClassPopUpOpen, setUpdateClassPopUpOpen] = useState(false);
	const [deleteClassPopUpOpen, setDeleteClassPopUpOpen] = useState(false);

	const [classes, setClasses] = useState([]);
	const [singleCLass, setSingleClass] = useState({});

	useEffect(() => {
		if (!user) {
			navigate("/auth/login");
		} else if (classes.length === 0) {
			getClasses();
		}
		console.log(classes);
	}, [classes]);

	const getClasses = () => {
		getAllCLasses({ token: user.token })
			.then((res) => {
				// console.log(res.data.data);
				setClasses(res.data.data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const addNewClass = () => {
		setCreateClassPopUpOpen(true);
	};

	const updateClass = (classItem) => {
		setSingleClass(classItem);
		setUpdateClassPopUpOpen(true);
	};

	const deleteClass = (classItem) => {
		setSingleClass(classItem);
		setDeleteClassPopUpOpen(true);
	};

	return (
		<div className="ModeratorPageContainer">
			<div className="ModeratorPageHeader">
				<div className="ModeratorPageHeaderText">Classes</div>
				<div className="ModeratorPageHeaderButtonArea">
					<ModButton
						btnText={"Add New Class"}
						onClick={addNewClass}
					/>
				</div>
			</div>
			<div className="ModeratorPageBody">
				<div className="ModeratorPageBodyTable">
					{classes.length > 0 && (
						<ModPageHeader items={["Class Name", "Action"]} />
					)}
					<div className="ModeratorPageBodyTableRow">
						{classes.map((classItem, index) => {
							return (
								<ModClassListItem
									key={index}
									classItem={classItem}
									updateClass={updateClass}
									deleteClass={deleteClass}
								/>
							);
						})}
					</div>
				</div>
			</div>
			{createClassPopUpOpen && (
				<CreateClassPopUp
					className="modal fade"
					id={idCreatePopUp}
					setCreateClassPopUpOpen={setCreateClassPopUpOpen}
					setClasses={setClasses}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}

			{updateClassPopUpOpen && (
				<UpdateClassPopUp
					className="modal fade"
					id={idUpdatePopUp}
					setUpdateClassPopUpOpen={setUpdateClassPopUpOpen}
					setClasses={setClasses}
					classItem={singleCLass}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}

			{deleteClassPopUpOpen && (
				<DeleteClassPopUp
					className="modal fade"
					id={idDeletePopUp}
					setDeleteClassPopUpOpen={setDeleteClassPopUpOpen}
					setClasses={setClasses}
					classItem={singleCLass}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}
		</div>
	);
}

export default ModeratorClassPage;
