import React, { useEffect, useState } from "react";
import { getAllCLasses } from "../../../../../api/class.api";
import { updateRoutineItem } from "../../../../../api/routine.api";
import { getEnabledSubjectsByClassID } from "../../../../../api/subject.api";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import "./selectClassSubject.scss";

function SelectClassSubject(props) {
	const [classes, setClasses] = useState([]);
	const [subjects, setSubjects] = useState([]);

	var user = loadStorage("user");

	useEffect(() => {
		if (classes.length === 0) {
			getAllCLasses({ token: user.token })
				.then((res) => {
					setClasses(res.data.data);
				})
				.catch((err) => {
					console.log(err);
				});
		}
	}, [classes]);

	useEffect(() => {
		setSubjects([]);

		classes.map((classItem) => {
			getEnabledSubjectsByClassID({
				token: user.token,
				classId: classItem.id,
			})
				.then((res) => {
					setSubjects((arr) => [...arr, ...res.data.data]);
				})
				.catch((err) => {
					console.log(err);
				});
		});
	}, [classes]);

	const handleUpdate = (e) => {
		updateRoutineItem({
			time: props.time,
			day: props.day,
			courseId: e.target.value,
			userId: user.id,
			token: user.token,
		})
			.then((res) => {
				console.log(res.data.message);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className="classSubjectContainer">
			<select className="classSubjectSelect" onChange={handleUpdate}>
				<option value="0">Select Class</option>
				{subjects.length > 0 &&
					subjects.map((subjectItem) => (
						<option
							key={subjectItem.id}
							value={subjectItem.id}
							selected={subjectItem.id === props.courseId}
						>
							{subjectItem?.subject?.class.class_name}
							{" - "}
							{subjectItem?.subject?.subject_name}
						</option>
					))}
			</select>
		</div>
	);
}

export default SelectClassSubject;
