import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "./TestPage.scss";
import { loadStorage } from "../../../../utils/persistLocalStorage";

import { getAllCLasses } from "../../../../api/class.api";
import ShowClasesTable from "./ShowClass/ShowClasesTable";
const TestPage = () => {
    const navigate = useNavigate();

    const [classes, setClasses] = useState([]);

    useEffect(() => {
        const user = loadStorage("user");
        if (!user) {
            navigate("/auth/login");
        } else {
            console.log(user.token);
            if (classes.length === 0) {
                getAllCLasses({ token: user.token })
                    .then((res) => {
                        console.log(res.data.data);
                        setClasses(res.data.data);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
            console.log("classes =>", classes);
        }
    }, [classes]);

    return (
        <div>
            <div className="Mod_table_Heading"> Moderator Dashboard</div>

            <div className="table_container_mod">
                {classes.map((classItem) => {
                    return (
                        <ShowClasesTable
                            key={classItem.id}
                            classItem={classItem}
                            classId={classItem.id}
                        />
                    );
                })}
            </div>
        </div>
    );
};

export default TestPage;
