import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
	Assignment,
	DashboardSharp,
	ExitToAppRounded,
	MenuBook,
	School,
} from "@material-ui/icons";
import { loadStorage, delStorage } from "../../../../utils/persistLocalStorage";
import ModNavbarItem from "../ModNavbarItem/ModNavbarItem";
import "./modNavbar.scss";

function ModNavbar() {
	const navigate = useNavigate();
	var user = loadStorage("user");
	const [currentPage, setCurrentPage] = useState(loadStorage("currentPage"));
	console.log(currentPage);

	useEffect(() => {
		user = loadStorage("user");
		if (user && user?.token) {
			console.log(user);
			console.log(user?.role);
		} else {
			console.log(user);
			navigate("/auth/login");
		}
	}, []);

	const handleLogout = () => {
		delStorage("user");
		navigate("/auth/login");
	};

	return (
		<div className="mod_navbar_container">
			<div className="mod_navbar_top">
				<div className="mod_navbar_profile_container">
					<img
						className="mod_navbar_profile_image"
						src={user.profile_picture}
						alt="img"
					/>
					<div>
						<div className="mod_navbar_profile_name">
							{user ? user.first_name + " " + user.last_name : ""}
						</div>
						<div className="mod_navbar_profile_role">Moderator</div>
					</div>
				</div>
			</div>

			<div className="mod_navbar_middle">
				<ModNavbarItem
					title={"Overview"}
					link={"/dashboard"}
					icon={<DashboardSharp className="menu_icon" />}
					focused={currentPage === "Overview"}
					setCurrentPage={setCurrentPage}
				/>
				<ModNavbarItem
					title={"Classes"}
					link={"/dashboard/classes"}
					icon={<School className="menu_icon" />}
					focused={currentPage === "Classes"}
					setCurrentPage={setCurrentPage}
				/>
				<ModNavbarItem
					title={"Subjects"}
					link={"/dashboard/subjects"}
					icon={<MenuBook className="menu_icon" />}
					focused={currentPage === "Subjects"}
					setCurrentPage={setCurrentPage}
				/>
				<ModNavbarItem
					title={"Chapters"}
					link={"/dashboard/chapters"}
					icon={<MenuBook className="menu_icon" />}
					focused={currentPage === "Chapters"}
					setCurrentPage={setCurrentPage}
				/>
				<ModNavbarItem
					title={"Materials"}
					link={"/dashboard/materials"}
					icon={<Assignment className="menu_icon" />}
					focused={currentPage === "Materials"}
					setCurrentPage={setCurrentPage}
				/>
				<ModNavbarItem
					title={"Development"}
					link={"/dashboard/development"}
					icon={<Assignment className="menu_icon" />}
					focused={currentPage === "Development"}
					setCurrentPage={setCurrentPage}
				/>
			</div>

			<div className="mod_navbar_bottom">
				<div
					className="mod_navbar_menu_btn logout_btn"
					onClick={handleLogout}
				>
					<ExitToAppRounded className="menu_icon" />
					Logout
				</div>
			</div>
		</div>
	);
}

export default ModNavbar;
