import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import TeacherDashboard from "../../TeacherPage/Dashboard/TeacherDashboard";
import { loadStorage } from "../../../utils/persistLocalStorage";
import ModeratorDashbord from "../../ModeratorPage/DashBoard/ModeratorDashboard";

const Dashboard = () => {
	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user?.token) {
			navigate("/auth/login");
		}
	}, [user]);

	return (
		<div>
			{user && user.role.is_admin ? (
				<ModeratorDashbord />
			) : user && user.role.is_teacher ? (
				<TeacherDashboard />
			) : (
				<div>Student Dashboard</div>
			)}
		</div>
	);
};

export default Dashboard;
