import { Bookmark, ClassOutlined, Delete, Edit } from "@material-ui/icons";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import ReactTooltip from "react-tooltip";
import { MAT_URL } from "../../../../../utils/urls";
import ModeratorTopicDetails from "../../ModeratorTopicDetails/ModeratorTopicDetails";

const ShowChaptersTable = ({ chapter, subjectItem, classItem }) => {
    const navigate = useNavigate();

    const takeToChapterPage = (subjectId) => {
        navigate(`mod/chapter/${chapter.id}`);
    };

    const [isOpen, setIsOpen] = useState(false);

    const togglePopup = () => {
        setIsOpen(!isOpen);
    };

    console.log(chapter);
    return (
        <div className="ShowChaptersTableContainer">
            {isOpen && (
                <ModeratorTopicDetails
                    chapter={chapter}
                    subjectItem={subjectItem}
                    classItem={classItem}
                    handleClose={togglePopup}
                />
            )}
            <div onClick={togglePopup} className="rowInfoHolder">
                <div className="vertical_bar_chapters" />
                <ClassOutlined
                    style={{
                        fontSize: "2rem",
                        padding: "0.5rem",
                    }}
                />
                {/* <div className="chapterIconLog">
                <img
                    src={MAT_URL + chapter.chapter_icon.file_path}
                    alt="chapter_icon"
                />
            </div> */}
                <div
                    data-tip
                    data-for="ChapterSeralTip"
                    className="ChapterSerial"
                >
                    Chapter Serial : {chapter.chapter_serial}
                </div>
                <div className="ChapterName">
                    Chapter Name :{chapter.chapter_name}
                </div>
                {/* <div className="Chapter_Cover_image_container">
				<img
					className="Chapter_Cover_image"
					style={{ height: "40px" }}
					src={MAT_URL + chapter.chapter_cover.file_path}
					alt="Chapter Cover"
				/>

			</div> */}
            </div>
            <div className="Chapter_Actions" onClick={console.log("clicked")}>
                <Edit />
                <Delete />
            </div>
            <ReactTooltip
                id="ChapterSeralTip"
                place="bottom"
                type="light"
                effect="solid"
            >
                Chapter Serial
            </ReactTooltip>
        </div>
    );
};

export default ShowChaptersTable;
