import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./moderatorChapterPage.scss";
import { getAllCLasses } from "../../../../api/class.api";
import { getEnabledSubjectsByClassID } from "../../../../api/subject.api";
import ModPageHeader from "../../components/ModPageHeader/ModPageHeader";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import { getModeratorCourseInfo } from "../../../../api/course.api";
import ModButton from "../../components/ModButton/ModButton";
import ModeratorChapterListItem from "../../components/ModeratorChapterListItem/ModeratorChapterListItem";
import CreateChapterPopUp from "../../components/popUps/chapterPopUps/CreateChapterPopUp";
import UpdateChapterPopUp from "../../components/popUps/chapterPopUps/UpdateChapterPopUp";
import DeleteChapterPopUp from "../../components/popUps/chapterPopUps/DeleteChapterPopUp";

const ModeratorChapterPage = () => {
	const user = loadStorage("user");
	const navigate = useNavigate();

	var classId, subjectId;
	const idCreateMessage = "create_chapter_popup";
	const idUpdateMessage = "update_chapter_popup";
	const idDeleteMessage = "delete_chapter_popup";

	const [inputs, setInputs] = useState({
		classId: "",
		className: "",
		subjectId: "",
		subjectName: "",
		token: user.token,
	});
	const [courseData, setCourseData] = useState({});
	const [classes, setClasses] = useState([]);
	const [subjects, setSubjects] = useState([]);
	const [chapters, setChapters] = useState([]);
	const [singleChapter, setSingleChapter] = useState({});
	const [createChapterPopUpOpen, setCreateChapterPopUpOpen] = useState(false);
	const [updateChapterPopUpOpen, setUpdateChapterPopUpOpen] = useState(false);
	const [deleteChapterPopUpOpen, setDeleteChapterPopUpOpen] = useState(false);

	useEffect(() => {
		if (!user) {
			navigate("/auth/login");
		} else {
			if (classes.length === 0) {
				getAllCLasses({ token: user.token })
					.then((res) => {
						console.log(res.data.data);
						setClasses(res.data.data);
					})
					.catch((err) => {
						console.log(err);
					});
			}
			console.log(classes);
		}
	}, [classes]);

	const handleClassFilters = async (e) => {
		classId = e.target.value;
		setInputs({ classId, subjectId: 0 });
		setCourseData({});
		setSubjects([]);
		setChapters([]);

		getEnabledSubjectsByClassID({ classId, token: user.token })
			.then((res) => {
				console.log(res.data.data);
				setSubjects(res.data.data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const handleSubjectFilters = async (e) => {
		subjectId = e.target.value;
		setInputs({ ...inputs, subjectId });

		getModeratorCourseInfo({ subjectId, token: user.token })
			.then((res) => {
				console.log(res.data.data);
				setCourseData(res.data.data.courseData);
				setChapters(res.data.data.chapters);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const addNewChapter = () => {
		setCreateChapterPopUpOpen(true);
	};

	const updateChapter = (chapter) => {
		setSingleChapter(chapter);
		setUpdateChapterPopUpOpen(true);
	};

	const deleteChapter = (chapter) => {
		setSingleChapter(chapter);
		setDeleteChapterPopUpOpen(true);
	};

	return (
		<div className="ModeratorPageContainer">
			<div className="ModeratorPageHeader">
				<div className="ModeratorPageHeaderFilterContainer">
					<select
						className="ModeratorPageHeaderFilterButton"
						defaultValue={"0"}
						onChange={handleClassFilters}
					>
						<option value="0">Select Class</option>
						{classes.length > 0 &&
							classes.map((classItem) => {
								return (
									<option
										key={classItem.id}
										value={classItem.id}
									>
										 {classItem.class_name}
									</option>
								);
							})}
					</select>

					<select
						className="ModeratorPageHeaderFilterButton"
						defaultValue={"0"}
						onChange={handleSubjectFilters}
					>
						<option value="0">Select Subject</option>
						{subjects.length > 0 &&
							subjects.map((subjectItem) => {
								return (
									<option
										key={subjectItem.id}
										value={subjectItem.subject.id}
									>
										 {subjectItem.subject.subject_name}
									</option>
								);
							})}
					</select>
				</div>

				<div className="course_data">
					<div className="course_info">
						<div className="course_info_item">
							Course ID: {courseData.id}
						</div>
						<div className="course_info_item">
							Course Code: {courseData.course_code}
						</div>
						<div className="course_info_item">
							Total Students: {courseData.number_of_students}
						</div>
						<div className="course_info_item">
							Total Quizes: {courseData.number_of_quizes}
						</div>
						<div className="course_info_item">
							Total Materials: {courseData.number_of_materials}
						</div>
					</div>
					<div className="add_chapter">
						{inputs.classId && inputs.subjectId && (
							<ModButton
								btnText="Add New Chapter"
								onClick={addNewChapter}
								dataToggle="modal"
								dataTarget={`#${idCreateMessage}`}
							/>
						)}
					</div>
				</div>
			</div>

			<div className="ModeratorPageBody">
				{chapters.length > 0 ? (
					<div className="ModeratorPageBodyTable">
						<ModPageHeader
							items={[
								"Chapters",
								"Chapter Serial",
								"Materials",
								"Comments",
								"Quizes",
								"Action",
							]}
						/>
						<div className="ModeratorPageBodyTableRow">
							{chapters.length > 0 &&
								chapters.map((chapter, index) => {
									return (
										<ModeratorChapterListItem
											key={index}
											chapter={chapter}
											updateChapter={updateChapter}
											deleteChapter={deleteChapter}
										/>
									);
								})}
						</div>
					</div>
				) : (
					" No chapter and class has been selected"
				)}
			</div>

			{createChapterPopUpOpen && (
				<CreateChapterPopUp
					className="modal fade"
					id={idCreateMessage}
					setCreateChapterPopUpOpen={setCreateChapterPopUpOpen}
					classId={inputs.classId}
					subjectId={inputs.subjectId}
					classes={classes}
					subjects={subjects}
					token={user.token}
					setChapters={setChapters}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}

			{updateChapterPopUpOpen && (
				<UpdateChapterPopUp
					className="modal fade"
					id={idUpdateMessage}
					setUpdateChapterPopUpOpen={setUpdateChapterPopUpOpen}
					chapterItem={singleChapter}
					classId={inputs.classId}
					subjectId={inputs.subjectId}
					classes={classes}
					subjects={subjects}
					token={user.token}
					setChapters={setChapters}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}

			{deleteChapterPopUpOpen && (
				<DeleteChapterPopUp
					className="modal fade"
					id={idDeleteMessage}
					setDeleteChapterPopUpOpen={setDeleteChapterPopUpOpen}
					chapterItem={singleChapter}
					subjectId={inputs.subjectId}
					token={user.token}
					setChapters={setChapters}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}
		</div>
	);
};

export default ModeratorChapterPage;
