import {
	CloudDownloadOutlined,
	DeleteForeverOutlined,
} from "@material-ui/icons";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import axios from "axios";
import fileDownload from "js-file-download";
import React, { useState } from "react";
import { MAT_URL } from "../../../../utils/urls";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import {
	deleteMaterial,
	getModeratorChapterInfoByChapterId,
} from "../../../../api/chapter.api";

const FileDownloadPage = ({ material, toggolSetUpdate }) => {
	const user = loadStorage("user");
	console.log("md:", material);
	const [downloadPercentage, setDownloadPercentage] = useState();

	const bytsToMB = (byts) => {
		console.log(byts);
		return (byts / 1024 / 1024).toFixed(2);
	};

	const handleDownloadRequest = async (filePath, filename) => {
		var fullPath = MAT_URL + filePath;

		axios({
			method: "get",
			url: fullPath,
			responseType: "blob",
			onDownloadProgress: (progressEvent) => {
				let percentCompleted = Math.round(
					(progressEvent.loaded * 100) / progressEvent.total
				);
				setDownloadPercentage(percentCompleted);
			},
		})
			.then((res) => {
				console.log(res);
				fileDownload(res.data, filename);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const handleDelete = (materialId, chapterId) => {
		deleteMaterial({ materialId, token: user.token })
			.then(function (res) {
				console.log("material deleted");
				toggolSetUpdate();
			})
			.catch(function (error) {
				console.log(error);
			});
	};

	const formatDateTime = (dateTime) => {
		const date = new Date(dateTime);
		const day = date.getDate();
		const monthList = [
			"Jan",
			"Feb",
			"Mar",
			"Apr",
			"May",
			"Jun",
			"July",
			"Aug",
			"Sep",
			"Oct",
			"Nov",
			"Dec",
		];
		const textMonth = monthList[date.getMonth()];
		const year = date.getFullYear();
		const hour = date.getHours();
		const minute = date.getMinutes();
		const ampm = hour >= 12 ? "PM" : "AM";
		const hour12 = hour % 12;
		const sHours = hour12 < 10 ? `0${hour12}` : hour12;
		const sMinutes = minute < 10 ? `0${minute}` : minute;
		const formattedTime = `${sHours}:${sMinutes} ${ampm}`;
		return `${day} ${textMonth}, ${year} ${formattedTime}`;
	};

	return (
		<div className="topic_header_chapter_Materials_component">
			<div className="materials_container">
				<div>Material Id : {material.id}</div>
				<div>
					Materials Size : {bytsToMB(material.materials?.size)}
					MB
				</div>
				<div>
					Upload Date :{" "}
					{formatDateTime(material.materials?.updated_at)}
				</div>
				<div>
					File Name :{" "}
					{
						material.materials?.file_path
							.split("/")
							[
								material.materials?.file_path.split("/")
									.length - 1
							].split("--")[1]
					}
				</div>
			</div>
			<div className="materials_download_container">
				{!downloadPercentage ? (
					<CloudDownloadOutlined
						className="download_icon"
						onClick={() => {
							handleDownloadRequest(
								material.materials.file_path,
								material.materials.file_path
									.split("/")
									[
										material.materials.file_path.split("/")
											.length - 1
									].split("--")[1]
							);
						}}
					/>
				) : (
					<div
						style={{
							width: 50,
							height: 50,
						}}
					>
						<CircularProgressbar
							value={downloadPercentage}
							text={`${downloadPercentage}%`}
						/>
					</div>
				)}
				{downloadPercentage === 100 && (
					<div
						style={{
							color: "aqua",
							fontSize: "12px",
							paddingTop: "5px",
						}}
					>
						Download Completed
					</div>
				)}
			</div>
			<div
				style={{
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
					paddingRight: "20px",
				}}
			>
				<DeleteForeverOutlined
					style={{
						fontSize: "40px",
					}}
					onClick={() => {
						handleDelete(material.id, material.chapter_id);
					}}
				/>
			</div>
		</div>
	);
};

export default FileDownloadPage;
