import React, { useEffect } from "react";

const ModeratorDevelopmentPostCard = ({
	report,
	setSelectedReport,
	selectedReport,
}) => {
	useEffect(() => {
		setSelectedReport(report);
	}, [report]);

	const getBGColor = (report) => {
		if (
			report.is_reported &&
			!report.is_acknowledged &&
			!report.is_in_progress &&
			!report.is_completed
		) {
			return ["Reported", "#EF7B6B"];
		} else if (
			report.is_acknowledged &&
			!report.is_in_progress &&
			!report.is_completed
		) {
			return ["Acknowledged", "#FFB248"];
		} else if (report.is_in_progress && !report.is_completed) {
			return ["In Progress", "#F4DE33"];
		} else if (report.is_completed) {
			return ["Completed", "#81CA72"];
		}
	};

	return (
		<div
			className="reportListItem"
			//set background color based on selectedReport.id === report.id
			style={{
				backgroundColor:
					selectedReport && selectedReport.id === report.id
						? "antiquewhite"
						: "",
			}}
			onClick={(e) => {
				setSelectedReport(report);
			}}
		>
			<div className="textContainer">
				Topic: <span>{report.topic}</span>
			</div>
			<div className="textContainer">Details: {report.details}</div>

			<div className="textContainer">
				Posted By : {report.user.first_name} {report.user.last_name}
			</div>
			<div className="textContainer">
				Posted Date : {new Date(report.created_at).toLocaleDateString()}
			</div>
			<div className="textContainer">
				Posted Time : {new Date(report.created_at).toLocaleTimeString()}
			</div>
			<div
				style={{
					display: "flex",
					justifyContent: "flex-start",
					alignItems: "center",
				}}
			>
				Status:
				<div
					className="progress_indicator"
					style={{
						backgroundColor: getBGColor(report)[1],
						height: "20px",
						padding: "3px",
						borderRadius: "5px",
						marginLeft: "5px",
					}}
				>
					{getBGColor(report)[0]}
				</div>
			</div>
			<div
				style={{
					display: "flex",
					justifyContent: "space-between",
				}}
			>
				{" "}
				<div>Upvote : {report.upvote_count}</div>
				<div>Downvote : {report.downvote_count}</div>
			</div>
		</div>
	);
};

export default ModeratorDevelopmentPostCard;
