import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Close, InfoOutlined } from "@material-ui/icons";
import "../../popUps/popUp.scss";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { createExam } from "../../../../../api/exam.api";

function CreateQuizPopUp(props) {
	const { setCreateQuizPopupOpen, chapter_x_subject_id, course_id } = props;

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const [quizTitle, setQuizTitle] = useState("");
	const [quizDescription, setQuizDescription] = useState("");
	const [error, setError] = useState("");

	const createQuiz = () => {
		if (quizTitle.length === 0) {
			setError("Quiz Title is required");
			return;
		} else if (quizDescription.length === 0) {
			setError("Quiz Description is required");
			return;
		}

		createExam({
			token: user.token,
			title: quizTitle,
			description: quizDescription,
			chapter_x_subject_id: chapter_x_subject_id,
			course_id: course_id,
		})
			.then((res) => {
				console.log(res);
				setCreateQuizPopupOpen(false);
				navigate("/forms/" + res.data.data.uuid);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className="popup_container">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the create course pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setCreateQuizPopupOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>

			<div className="popup_title">
				<h2>Create New Quiz</h2>
			</div>

			<div className="form">
				<div className="popup_form_field">
					<label>Quiz Title</label>
					<input
						type="text"
						title="title"
						placeholder="Enter quiz title"
						onChange={(e) => {
							setQuizTitle(e.target.value);
						}}
					/>
				</div>
				<div className="popup_form_field">
					<label>Quiz Description</label>
					<input
						type="text"
						title="description"
						placeholder="Enter quiz description"
						onChange={(e) => {
							setQuizDescription(e.target.value);
						}}
					/>
				</div>

				<div className="popup_error">{error && <p>{error}</p>}</div>
			</div>

			<div className="popup_btn_row">
				<span className="popup_btn" onClick={createQuiz}>
					Create
				</span>

				<span
					className="popup_btn"
					onClick={() => {
						setCreateQuizPopupOpen(false);
					}}
				>
					Cancel
				</span>
			</div>
		</div>
	);
}

export default CreateQuizPopUp;
