import React from "react";
import "./moderatorChapterListItem.scss";
import ModButton from "../ModButton/ModButton";

const ModeratorChapterListItem = (props) => {
	const { chapter, updateChapter, deleteChapter } = props;
	
	return (
		<div className="ModeratorChapterListComponent_Container">
			<div className="ModeratorChapterListComponent_Container_Name">
				{chapter.chapter_name}
			</div>
			<div className="ModeratorChapterListComponent_Container_Name">
				{chapter.chapter_serial}
			</div>
			<div className="ModeratorChapterListComponent_Container_Name">
				Materials
			</div>
			<div className="ModeratorChapterListComponent_Container_Name">
				Comments
			</div>
			<div className="ModeratorChapterListComponent_Container_Name">
				Quizes
			</div>
			<div className="ModeratorChapterListComponent_Container_Actions">
				<ModButton
					btnText="Edit"
					onClick={() => {
						updateChapter(chapter);
					}}
				/>
				<ModButton
					btnText="Delete"
					onClick={() => {
						deleteChapter(chapter);
					}}
				/>
			</div>
		</div>
	);
};

export default ModeratorChapterListItem;
