import React, { useEffect, useState } from "react";
import "./formPage.scss";
import FormBody from "../../../../components/form_body/FormBody";
import FormHeader from "../../../../components/form_header/FormHeader";
import FormTab from "../../../../components/form_tab/FormTab";
import { getFormData } from "../../../../api/exam.api";
import { loadStorage } from "../../../../utils/persistLocalStorage";

function FormPage() {
	const uuid = window.location.pathname.split("/")[2];
	const [formData, setFormData] = useState({});
	const [notFound, setNotFound] = useState(false);
	const [loading, setLoading] = useState(true);

	var user = loadStorage("user");
	useEffect(() => {
		user = loadStorage("user");
		getFormData({ token: user.token, uuid: uuid })
			.then((res) => {
				console.log(res.data.data);
				setFormData(res.data.data);
				setLoading(false);
			})
			.catch((err) => {
				console.log(err);
				setNotFound(true);
				setLoading(false);
			});
	}, []);

	return (
		// {notFound ? () :
		<div className="form_page_container">
			{notFound ? (
				<div>
					<span>Not Found</span>
				</div>
			) : (
				!loading && (
					<>
						<div className="form_page_header_container">
							{/* <FormHeader uuid /> */}
							{/* <FormTab /> */}
						</div>
						<FormBody user={user} formData={formData} />
					</>
				)
			)}
		</div>
	);
}

export default FormPage;
