import { Close, InfoOutlined } from "@material-ui/icons";
import React, { useEffect } from "react";
import { deleteChapter } from "../../../../../api/chapter.api";
import { getModeratorCourseInfo } from "../../../../../api/course.api";
import ModButton from "../../ModButton/ModButton";

function DeleteChapterPopUp(props) {
	const { setDeleteChapterPopUpOpen, token, subjectId, setChapters, chapterItem } =
		props;
	console.log(props);

	const handleDelete = () => {
		console.log("delete chapter");

		deleteChapter({
			token,
			chapterId: chapterItem.id,
		})
			.then((res) => {
				console.log(res);
				setDeleteChapterPopUpOpen(false);
					getModeratorCourseInfo({
						subjectId,
						token,
					})
						.then((res) => {
							console.log(res.data.data);
							setChapters(res.data.data.chapters);
						})
						.catch((err) => {
							console.log(err);
						});
			})
			.catch((err) => {
				console.log(err);
			});
	};
	return (
		<div className="ModeratorFormContainer">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the delete class pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setDeleteChapterPopUpOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>

			<div className="ModeratorFormInputsContainer">
				<div className="form_row">
					<p>
						Do you want to delete{" "}
						<span>
							<b>{chapterItem.chapter_name}</b>
						</span>
						?
					</p>
				</div>

				<div className="ModeratorFormBtns">
					<ModButton btnText={"Delete"} onClick={handleDelete} />
					<ModButton
						btnText="Cancel"
						onClick={() => {
							setDeleteChapterPopUpOpen(false);
						}}
					/>
				</div>
			</div>
		</div>
	);
}

export default DeleteChapterPopUp;
