import React, { useEffect, useState } from "react";
import "./moderatorDevelopment.scss";
import { useNavigate } from "react-router-dom";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import axios, { Axios } from "axios";
import {
    DELETE_REPORT_URL,
    GET_N_REPORT_URL,
    UPDATE_REPORT_URL,
} from "../../../../utils/urls";
import { ArrowLeft, ArrowRight } from "@material-ui/icons";
import ModeratorDevelopmentPostCard from "./ModeratorDevelopmentPostCard";
import ColorSelectBtn from "../../components/ColorSelectorBtn/ColorSelectBtn";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const ModeratorDevelopment = () => {
    const user = loadStorage("user");
    const [page, setPage] = useState(1);
    const [reload, setReload] = useState(false);

    const [reports, setReports] = useState([]);
    const [totalPage, setTotalPage] = useState(1);
    const [selectedReport, setSelectedReport] = useState();
    const [changeStatus, setChangeStatus] = useState(false);
    const [startDate, setStartDate] = useState(new Date());

    const [updateRepStatus, setUpdateRepStatus] = useState({
        topic: "",
        details: "",
        is_reported: false,
        is_acknowledged: false,
        is_in_progress: false,
        is_completed: false,
    });

    //sort reports by date and time
    const sortReports = (reports) => {
        return reports.sort((a, b) => {
            return (
                new Date(b.updated_at).getDate() -
                new Date(a.updated_at).getDate()
            );
        });
    };

    useEffect(() => {
        reports.map((report) => {
            // console.log("startDate", startDate);
            // console.log("updated_at", new Date(report.updated_at));
            // console.log(
            //     startDate.getDate() === new Date(report.updated_at).getDate()
            // );
            if (startDate.getDate() === new Date(report.updated_at).getDate()) {
                console.log("same date");
                return setReports(sortReports(reports));
            } else {
                console.log("not same date");
                return setReports(sortReports(reports));
            }
        });
    }, [startDate]);

    useEffect(() => {
        if (selectedReport) {
            setUpdateRepStatus({
                topic: selectedReport.topic,
                details: selectedReport.details,
                is_reported: selectedReport.is_reported,
                is_acknowledged: selectedReport.is_acknowledged,
                is_in_progress: selectedReport.is_in_progress,
                is_completed: selectedReport.is_completed,
            });
        }
    }, [selectedReport]);

    // update report
    const updateReportStatusHandeler = (option) => {
        if (option === "Acknowledged") {
            setUpdateRepStatus({
                ...updateRepStatus,
                is_acknowledged: true,
                is_in_progress: false,
                is_completed: false,
            });
        } else if (option === "In_Progress") {
            setUpdateRepStatus({
                ...updateRepStatus,
                is_acknowledged: true,
                is_in_progress: true,
                is_completed: false,
            });

            console.log(updateRepStatus);
        } else if (option === "Completed") {
            setUpdateRepStatus({
                ...updateRepStatus,
                is_acknowledged: true,
                is_in_progress: true,
                is_completed: true,
            });
        }
    };

    const updateReportStatus = () => {
        var config = {
            method: "patch",
            headers: {
                "Content-Type": "application/json",
                "x-auth-token": user.token,
            },
            data: updateRepStatus,
            url: UPDATE_REPORT_URL + "/" + `${selectedReport.id}`,
        };

        axios(config)
            .then((response) => {
                console.log(response);
                setReload(!reload);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const deleteReportHandeler = (report) => {
        var config = {
            method: "delete",
            headers: {
                "x-auth-token": user.token,
            },
            url: DELETE_REPORT_URL + "/" + `${selectedReport.id}`,
        };
        axios(config)
            .then((response) => {
                console.log(response);
                setReload(!reload);
            })
            .catch((error) => {
                console.log(error);
            });
    };

    const navigate = useNavigate();

    useEffect(() => {
        if (!user) {
            navigate("/auth/login");
        } else {
            axios({
                method: "get",
                url: GET_N_REPORT_URL + "/" + `${page}`,
                headers: {
                    "Content-Type": "application/json",
                    "x-auth-token": user.token,
                },
            })
                .then((res) => {
                    setReports(sortReports(res.data.data.reportData));
                    setTotalPage(res.data.data.totalPage);
                    // setIsLoading(false);
                    console.log(res.data.data.reportData);
                })
                .catch((err) => {
                    console.log(err);
                    // setIsLoading(false);
                });
        }
    }, [page, reload]);

    const getBGColor = (report) => {

        if (
            report.is_reported &&
            !report.is_acknowledged &&
            !report.is_in_progress &&
            !report.is_completed
        ) {
            return "#EF7B6B";
        } else if (
            report.is_acknowledged &&
            !report.is_in_progress &&
            !report.is_completed
        ) {
            return "#FFB248";
        } else if (report.is_in_progress && !report.is_completed) {
            return "#F4DE33";
        } else if (report.is_completed) {
            return "#81CA72";
        }
    };

    const handleLeftRight = (e) => {
        if (e === "left") {
            console.log(e);
            if (page > 1) {
                setPage(page - 1);
            }
        } else if (e === "right") {
            if (page < totalPage) {
                setPage(page + 1);
            }
        }
    };
    return (
        <div className="ModeratorDevelopmentContainer">
            <div className="left">
                <div className="left_top">
                    <h2>Posts</h2>
                    <DatePicker
                        selected={startDate}
                        onChange={(date) => setStartDate(date)}
                    />
                    <div className="arrows">
                        <ArrowLeft
                            className="Arrow"
                            onClick={(e) => handleLeftRight("left")}
                        />

                        <ArrowRight
                            className="Arrow"
                            onClick={(e) => handleLeftRight("right")}
                        />
                    </div>
                </div>
                <div className="postContainer">
                    {reports.map((report, index) => {
                        return (
                            <ModeratorDevelopmentPostCard
                                key={index}
                                report={report}
                                setSelectedReport={setSelectedReport}
                                selectedReport={selectedReport}
                            />
                        );
                    })}
                </div>
            </div>
            <div className="mid">
                {selectedReport ? (
                    <div>
                        <div className="label_edit_post">Edit Post </div>
                        <div className="post_card">
                            <div
                                style={{
                                    display: "flex",
                                    flexDirection: "row",
                                    justifyContent: "space-between",
                                    alignItems: "center",
                                    width: "100%",
                                }}
                            >
                                <input
                                    type="text"
                                    value={selectedReport.topic}
                                />
                                <div> 2 | 5 </div>
                            </div>

                            <textarea
                                type="text"
                                value={selectedReport.details}
                                disabled
                            />

                            <div className="status_container">
                                <div className="currentStatus">
                                    <div>Status:</div>
                                    <div
                                        className="progress_indicator"
                                        style={{
                                            backgroundColor:
                                                getBGColor(updateRepStatus),
                                            height: "20px",
                                            width: "60px",
                                            borderRadius: "5px",
                                        }}
                                    />
                                </div>

                                <div
                                    className="changeStatusBtn"
                                    onClick={() =>
                                        setChangeStatus(!changeStatus)
                                    }
                                >
                                    Change Status
                                </div>
                            </div>

                            {changeStatus && (
                                <div className="options_container">
                                    <ColorSelectBtn
                                        backgroundColor="#EF7B6B"
                                        // options={updateReportStatusHandeler}
                                        setOptions="Reported"
                                        text="Reported"
                                        isDisabled={true}
                                    />
                                    <ColorSelectBtn
                                        backgroundColor="#FFB248"
                                        options={updateReportStatusHandeler}
                                        setOptions="Acknowledged"
                                        text="Acknowledged"
                                        isDisabled={false}
                                    />
                                    <ColorSelectBtn
                                        backgroundColor="#F4DE33"
                                        options={updateReportStatusHandeler}
                                        setOptions="In_Progress"
                                        text="In Progress"
                                        isDisabled={false}
                                    />
                                    <ColorSelectBtn
                                        backgroundColor="#81CA72"
                                        options={updateReportStatusHandeler}
                                        setOptions="Completed"
                                        text="Completed"
                                        isDisabled={false}
                                    />
                                </div>
                            )}

                            <div className="post_footer">
                                posted by : {selectedReport.user.first_name}{" "}
                                {selectedReport.user.last_name}
                                <br />
                                Posted Date :{" "}
                                {new Date(
                                    selectedReport.created_at
                                ).toLocaleDateString()}
                                <br />
                                Posted Time :{" "}
                                {new Date(
                                    selectedReport.created_at
                                ).toLocaleTimeString()}
                                <br />
                                updated at :{" "}
                                {new Date(
                                    selectedReport.updated_at
                                ).toLocaleDateString()}{" "}
                                -{" "}
                                {new Date(
                                    selectedReport.updated_at
                                ).toLocaleTimeString()}
                            </div>
                        </div>
                        <div className="btn_holder">
                            <button
                                className="btn_edit"
                                onClick={() => {
                                    setChangeStatus(false);
                                    updateReportStatus();
                                }}
                            >
                                Update
                            </button>
                            <button
                                className="btn_delete"
                                onClick={() => {
                                    deleteReportHandeler(selectedReport.id);
                                }}
                            >
                                Delete
                            </button>
                        </div>
                    </div>
                ) : (
                    <div
                        style={{
                            color: "#fff",
                            fontSize: "40px",
                        }}
                    >
                        No Post
                    </div>
                )}
            </div>
            {/* <div className="right">Mod post btn</div> */}
        </div>
    );
};

export default ModeratorDevelopment;
