import React, { useState } from "react";
import "./showBooks.scss";
import axios from "axios";
import { MAT_URL } from "../../../../utils/urls";
import fileDownload from "js-file-download";
import { CircularProgressbar } from "react-circular-progressbar";
import {
	CloudDownloadOutlined,
	CheckCircleOutline,
	Launch,
} from "@material-ui/icons";

function ShowBooks({ material, setIsLoading }) {
	const [downloadPercentage, setDownloadPercentage] = useState();
	const newDate = new Date(material.updated_at);
	const uploadedDate = newDate.toLocaleDateString();

	const handleOpenRequest = (filePath, filename) => {
		var fullPath = MAT_URL + filePath;
		window.open(fullPath, "_blank");
	};

	const handleDownloadRequest = async (filePath, filename) => {
		var fullPath = MAT_URL + filePath;

		axios({
			method: "get",
			url: fullPath,
			responseType: "blob",
			onDownloadProgress: (progressEvent) => {
				let percentCompleted = Math.round(
					(progressEvent.loaded * 100) / progressEvent.total
				);
				setDownloadPercentage(percentCompleted);
			},
		})
			.then((res) => {
				console.log(res);
				fileDownload(res.data, filename);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const bytsToMB = (byts) => {
		console.log(byts);
		return (byts / 1024 / 1024).toFixed(2);
	};

	const formatDateTime = (dueDateTime) => {
		// format => 14 July 2022 7:00 PM
		const date = new Date(dueDateTime);
		const day = date.getDate();
		const month = date.getMonth() + 1;
		const monthList = [
			"Jan",
			"Feb",
			"Mar",
			"Apr",
			"May",
			"Jun",
			"July",
			"Aug",
			"Sep",
			"Oct",
			"Nov",
			"Dec",
		];
		const textMonth = monthList[date.getMonth()];
		const year = date.getFullYear();
		return `${day} ${textMonth}, ${year}`;
	};

	return (
		<div>
			<div className="material_container">
				<div className="material_container_thumbnail">
					<img
						src={MAT_URL + material.thumb.file_path}
						alt="thumbnail"
						className="material_container_thumbnail_img"
					/>
				</div>
				<div className="material_container_middle">
					<div className="material_name">
						File Name : {material.books.title}
					</div>
					<div className="material_type">
						File Type : {material.books.file_type}
					</div>
					<div className="material_size">
						File Size : {bytsToMB(material.books.size)}MB
					</div>
					<div className="material_updateDate">
						Uploaded on : {formatDateTime(uploadedDate)}
					</div>
				</div>
				<div className="material_container_middle_right">
					{material.books.uploder.role.is_admin && (
						<CheckCircleOutline
							style={{
								color: "green",
								fontSize: "2rem",
							}}
						/>
					)}

					<div>
						{material.books.uploder.role.is_admin
							? "TSC Material"
							: "Teacher's Material"}
					</div>
				</div>
				<div className="material_container_right">
					<div
						style={{
							display: "flex",
							flexDirection: "row",
							justifyContent: "space-between",
							alignItems: "center",
							padding: "5px",
							borderRadius: "5px",
							backgroundColor: "#46178f",
							margin: "5px",
						}}
						onClick={() => {
							handleOpenRequest(
								material.books.file_path,
								material.books.file_path
									.split("/")
									[
										material.books.file_path.split("/")
											.length - 1
									].split("--")[1]
							);
						}}
					>
						<div className="iconHolder">Open</div>{" "}
						<Launch className="icon" />
					</div>

					<div>
						{bytsToMB(material.books.size) > 0 ? (
							<div>
								{!downloadPercentage ? (
									<div
										style={{
											display: "flex",
											flexDirection: "row",
											justifyContent: "space-between",
											alignItems: "center",
											padding: "5px",
											borderRadius: "5px",
											backgroundColor: "#46178f",
											margin: "5px",
										}}
										onClick={() => {
											handleDownloadRequest(
												material.books.file_path,
												material.books.file_path
													.split("/")
													[
														material.books.file_path.split(
															"/"
														).length - 1
													].split("--")[1]
											);
										}}
									>
										<div className="iconHolder">
											Download
										</div>{" "}
										<CloudDownloadOutlined className="icon" />
									</div>
								) : (
									<div
										style={{
											width: 40,
											height: 40,
										}}
										onClick={() => {
											setDownloadPercentage(0);
										}}
									>
										<CircularProgressbar
											value={downloadPercentage}
											text={`${downloadPercentage}%`}
										/>
									</div>
								)}
							</div>
						) : (
							<div
								style={{
									color: "red",
									fontSize: "12px",
									paddingTop: "5px",
								}}
							>
								Download Not Available
							</div>
						)}
					</div>
				</div>
			</div>
		</div>
	);
}

export default ShowBooks;
