import React from "react";
import "./moderatorSubjectListItem.scss";
import ModButton from "../ModButton/ModButton";
import { MAT_URL } from "../../../../utils/urls";

function ModeratorSubjectListItem(props) {
	const { subjectItem, updateSubject, deleteSubject } = props;

	return (
		<div className="ModSubjectListItemContainer">
			<>
				<div className="ModSubjectListItemIcon">
					<img
						src={
							MAT_URL + subjectItem.subject.subject_icon.file_path
						}
						alt="subject_icon"
					/>
				</div>
				<div className="ModSubjectListItemName">
					{subjectItem.subject.subject_name}
				</div>
				<div className="ModSubjectListItemActions">
					<ModButton btnText="View" />
					<ModButton
						btnText="Edit"
						onClick={() => {
							updateSubject(subjectItem);
						}}
					/>
					<ModButton
						onClick={() => {
							deleteSubject(subjectItem);
						}}
						btnText="Delete"
					/>
				</div>
			</>
		</div>
	);
}

export default ModeratorSubjectListItem;
