import React from "react";
import "./modPageHeader.scss";

function ModPageHeader(props) {
	const { items } = props;

	return (
		<div className="ModeratorCourseListHeader_Container">
			{items.map((item, index) => {
				return (
					<div
						className="ModeratorCourseListHeader_Container_Name"
						key={index}
					>
						{item}
					</div>
				);
			})}
		</div>
	);
}

export default ModPageHeader;
