import React, { useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import "../../popUps/popUp.scss";
import {
	delStorage,
	saveStorage,
} from "../../../../../utils/persistLocalStorage";
import { updateUser } from "../../../../../api/user.api";

function EditProfilePopUp({
	setEditProfile,
	userDetails,
	setUserDetails,
	user,
}) {
	const [address, setAddress] = useState(userDetails.address);
	const [phone, setPhone] = useState(userDetails.phone);
	const [bio, setBio] = useState(userDetails.about_me);
	const [error, setError] = useState("");

	const handleSubmit = (e) => {
		e.preventDefault();

		updateUser({
			token: user.token,
			userId: userDetails.id,
			address: address,
			phone: phone,
			bio: bio,
		})
			.then((res) => {
				console.log(res.data.data);
				var newUser = res.data.data;
				delStorage("user");
				saveStorage("user", res.data.data);
				setUserDetails(newUser);
				setEditProfile(false);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className="popup_container">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert(
								"You can use this window to edit your profile"
							);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setEditProfile(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>

			<div className="popup_title">
				<h2>Update Profile</h2>
			</div>

			<div className="form">
				<div className="popup_form_field">
					<label>Phone</label>
					<input
						type="text"
						title="Write your phone number here"
						placeholder="Phone Number"
						value={phone}
						onChange={(e) => {
							setPhone(e.target.value);
						}}
					/>
				</div>

				<div className="popup_form_field">
					<label>Address</label>
					<input
						type="text"
						title="Write your address here"
						placeholder="Address"
						value={address}
						onChange={(e) => {
							setAddress(e.target.value);
						}}
					/>
				</div>

				<div className="popup_form_field">
					<label>About You</label>
					<textarea
						title="Write something about yourself"
						placeholder="About Yourself"
						value={bio}
						onChange={(e) => {
							setBio(e.target.value);
						}}
					></textarea>
				</div>

				<div className="popup_error">{error && <p>{error}</p>}</div>
			</div>

			<div className="popup_btn_row">
				<span
					className="popup_btn"
					title="Update your data"
					onClick={handleSubmit}
				>
					Update
				</span>

				<span
					className="popup_btn"
					title="Go back to profile"
					onClick={() => {
						setEditProfile(false);
					}}
				>
					Cancel
				</span>
			</div>
		</div>
	);
}

export default EditProfilePopUp;
