import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import "./App.scss";
import LoginPage from "./pages/CommonPage/Authentication/LoginPage";
import RegistrationPage from "./pages/CommonPage/Authentication/RegistrationPage";
import ResetPassword from "./pages/CommonPage/Authentication/ResetPassword";
import LandingPage from "./pages/CommonPage/LandingPage/LandingPage";
import Dashboard from "./pages/CommonPage/Dashboard/Dashboard";
import ClassPage from "./pages/TeacherPage/nav_class/class_wise_subjects/ClassPage";
import ChapterDetails from "./pages/TeacherPage/nav_class/chapter_details/ChapterDetails";
import ChapterPage from "./pages/TeacherPage/nav_class/chapters/ChapterPage";
import ModeratorTopicDetails from "./pages/ModeratorPage/pages/ModeratorTopicDetails/ModeratorTopicDetails";
import TeachersProfile from "./pages/TeacherPage/Profile/TeachersProfile";
import SettingsPage from "./pages/CommonPage/Settings/SettingsPage";
import DevelopmentPage from "./pages/CommonPage/Development/DevelopmentPage";
import FormPage from "./pages/CommonPage/FormMaker/form_page/FormPage";
import ModeratorDashbord from "./pages/ModeratorPage/DashBoard/ModeratorDashboard";
import TestPage from "./pages/ModeratorPage/pages/TestPages/TestPage";
import ModeratorClassPage from "./pages/ModeratorPage/pages/ModeratorClassPage/ModeratorClassPage";
import ModeratorSubjectPage from "./pages/ModeratorPage/pages/ModeratorSubjectPage/ModeratorSubjectPage";
import ModeratorChapterPage from "./pages/ModeratorPage/pages/ModeratorChapterPage/ModeratorChapterPage";
import ModeratorMaterialPage from "./pages/ModeratorPage/pages/ModeratorMaterialPage/ModeratorMaterialPage";
import ModeratorDevelopment from "./pages/ModeratorPage/pages/ModeratorDevelopmentPages/ModeratorDevelopment";

function App() {
	return (
		<div className="App">
			<Router>
				<Routes>
					{/* Common Routes */}
					<Route exact path="/" element={<LandingPage />} />
					<Route
						path="/auth/registration"
						element={<RegistrationPage />}
					/>
					<Route path="/auth/login" element={<LoginPage />} />
					<Route
						path="/auth/reset-password"
						element={<ResetPassword />}
					/>

					{/* Teacher Routes */}
					<Route path="/dashboard" element={<Dashboard />} />
					<Route path="/class" element={<ClassPage />} />
					<Route path="/course/:courseId" element={<ChapterPage />} />
					<Route
						path="/chapter/:chapterId"
						element={<ChapterDetails />}
					/>
					<Route
						path="mod/chapter/:chapterId"
						element={<ModeratorTopicDetails />}
					/>
					<Route
						path="teacher/profile"
						element={<TeachersProfile />}
					/>
					<Route path="forms/:formId" element={<FormPage />} />
					<Route path="/settings" element={<SettingsPage />} />
					<Route path="/development" element={<DevelopmentPage />} />

					{/* Moderator Routes */}
					<Route path="/dashboard" element={<Dashboard />}>
						<Route index element={<TestPage />} />
						<Route
							path="classes"
							element={<ModeratorClassPage />}
						/>
						<Route
							path="subjects"
							element={<ModeratorSubjectPage />}
						/>

						<Route
							path="chapters"
							element={<ModeratorChapterPage />}
						/>
						<Route
							path="materials"
							element={<ModeratorMaterialPage />}
						/>
						<Route
							path="development"
							element={<ModeratorDevelopment />}
						/>
					</Route>
				</Routes>
			</Router>
		</div>
	);
}

export default App;
