import axios from "axios";
import {
	CREATE_ROUTINE_ITEM_URL,
	DELETE_ROUTINE_ITEM_URL,
	GET_CLASS_SCHEDULE,
	GET_FULL_ROUTINE_URL,
	UPDATE_ROUTINE_ITEM_URL,
} from "../utils/urls";

export const getClassSchedule = (data) => {
	return axios({
		method: "post",
		url: GET_CLASS_SCHEDULE,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
		data: data,
	});
};

export const getFullRoutine = (data) => {
	return axios({
		method: "get",
		url: GET_FULL_ROUTINE_URL + "/" + data.userId,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
	});
};

export const createRoutineItem = (data) => {
	console.log(data);
	return axios({
		method: "post",
		url: CREATE_ROUTINE_ITEM_URL,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
		data: data,
	});
};

export const updateRoutineItem = (data) => {
	return axios({
		method: "patch",
		url: UPDATE_ROUTINE_ITEM_URL,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
		data: data,
	});
};

export const deleteRoutineItem = (data) => {
	return axios({
		method: "delete",
		url: DELETE_ROUTINE_ITEM_URL,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
		data: data,
	});
};
