import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import "./moderatorMaterialPage.scss";
import { getAllCLasses } from "../../../../api/class.api";
import { getEnabledSubjectsByClassID } from "../../../../api/subject.api";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import { getModeratorCourseInfo } from "../../../../api/course.api";
import MaterialsPopUp from "../../components/popUps/materialsPopUps/CreateMaterialsPopUp";
import axios from "axios";
import fileDownload from "js-file-download";
import { MAT_URL } from "../../../../utils/urls";
import { Help } from "@material-ui/icons";
import ModMaterialsItems from "../../components/ModMaterialsItems/ModMaterialsItems";
import {
    deleteMaterial,
    getModeratorChapterInfoByChapterId,
} from "../../../../api/chapter.api";

function ModeratorMaterialPage() {
    const user = loadStorage("user");
    const navigate = useNavigate();

    const [showCreateChapterPopUp, setShowCreateChapterPopUp] = useState(false);
    const [showCreateChapterPopUpBtn, setShowCreateChapterPopUpBtn] =
        useState(false);

    var classId, subjectId, chapterId;

    const [inputs, setInputs] = useState({
        classId: "",
        subjectId: "",
        courseId: "",
        chapterId: "",
        token: user.token,
    });
    const [classes, setClasses] = useState([]);
    const [subjects, setSubjects] = useState([]);
    const [course, setCourse] = useState({});
    const [chapters, setChapters] = useState([]);
    const [chapterMaterials, setChapterMaterials] = useState({
        chapterData: {},
        chapterMaterials: [],
    });

    useEffect(() => {
        if (!user) {
            navigate("/auth/login");
        } else {
            if (classes?.length === 0) {
                getAllCLasses({ token: user.token })
                    .then((res) => {
                        // console.log(res.data.data);
                        setClasses(res.data.data);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }
            console.log(classes);
        }
    }, [classes]);

    const handleClassFilters = async (e) => {
        e.preventDefault();
        setShowCreateChapterPopUpBtn(false);

        classId = e.target.value;
        console.log(classId);
        setInputs({
            classId: classId,
            subjectId: 0,
            chapterId: 0,
        });
        setSubjects([]);
        setChapters([]);
        setChapterMaterials({
            chapterData: {},
            chapterMaterials: [],
        });

        getEnabledSubjectsByClassID({ classId, token: user.token })
            .then((res) => {
                console.log("Enabled", res.data.data);
                setSubjects(res.data.data);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const handleSubjectFilters = async (e) => {
        e.preventDefault();
        setShowCreateChapterPopUpBtn(false);

        subjectId = e.target.value;
        console.log(subjectId);
        setInputs({ ...inputs, subjectId, chapterId: 0 });
        setChapters([]);
        setChapterMaterials({
            chapterData: {},
            chapterMaterials: [],
        });

        getModeratorCourseInfo({ subjectId, token: user.token })
            .then((res) => {
                console.log(res.data.data);
                setCourse(res.data.data.courseData);
                setInputs({ ...inputs, courseId: res.data.data.courseData.id });
                setChapters(res.data.data.chapters);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const handleChapterFilters = async (e) => {
        e.preventDefault();

        chapterId = e.target.value;
        console.log(chapterId);
        setInputs({ ...inputs, chapterId });

        getModeratorChapterInfoByChapterId({ chapterId, token: user.token })
            .then((res) => {
                setChapterMaterials({
                    chapterData: res.data.data.chapter.chapterData,
                    chapterMaterials: res.data.data.chapter.chapterMaterials,
                });
                console.log(res.data.data.chapter.chapterData);
                console.log(res.data.data.chapter.chapterMaterials);
                setShowCreateChapterPopUpBtn(true);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    const handleDelete = (e, materialId, chapterId) => {
        e.preventDefault();

        console.log(materialId);

        deleteMaterial({ materialId, token: user.token })
            .then(function (res) {
                getModeratorChapterInfoByChapterId({
                    chapterId,
                    token: user.token,
                })
                    .then((res) => {
                        setChapterMaterials({
                            chapterData: res.data.data.chapter.chapterData,
                            chapterMaterials:
                                res.data.data.chapter.chapterMaterials,
                        });
                        console.log(res.data.data.chapter.chapterData);
                        console.log(res.data.data.chapter.chapterMaterials);
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            })
            .catch(function (error) {
                console.log(error);
            });
    };

    const handleDownloadRequest = async (e, filePath, filename) => {
        e.preventDefault();

        var fullPath = MAT_URL + filePath;

        axios({
            method: "get",
            url: fullPath,
            responseType: "blob",
        })
            .then((res) => {
                console.log(res);
                fileDownload(res.data, filename);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    return (
        <div className="ModeratorPageContainer">
            <div className="ModeratorPageHeader">
                <div className="ModeratorPageHeaderFilterContainer">
                    <select
                        className="ModeratorPageHeaderFilterButton"
                        defaultValue={"0"}
                        onChange={handleClassFilters}
                    >
                        <option value="0">Select Class</option>
                        {classes?.length > 0 &&
                            classes?.map((classItem) => {
                                return (
                                    <option
                                        key={classItem.id}
                                        value={classItem.id}
                                    >
                                         {classItem.class_name}
                                    </option>
                                );
                            })}
                    </select>

                    <select
                        className="ModeratorPageHeaderFilterButton"
                        defaultValue={"0"}
                        onChange={handleSubjectFilters}
                    >
                        <option value="0">Select Subject</option>
                        {subjects?.length > 0 &&
                            subjects?.map((subjectItem) => {
                                return (
                                    <option
                                        key={subjectItem.id}
                                        value={subjectItem.subject.id}
                                    >
                                         {subjectItem.subject.subject_name}
                                    </option>
                                );
                            })}
                    </select>

                    <select
                        className="ModeratorPageHeaderFilterButton"
                        defaultValue={"0"}
                        onChange={handleChapterFilters}
                    >
                        <option value="0">Select Chapter</option>
                        {chapters?.length > 0 &&
                            chapters?.map((chapterItem) => {
                                return (
                                    <option
                                        key={chapterItem.id}
                                        value={chapterItem.id}
                                    >
                                         {chapterItem.chapter_name}
                                        {/* {chapterItem.id} */}
                                    </option>
                                );
                            })}
                    </select>
                </div>

                {showCreateChapterPopUp && (
                    <MaterialsPopUp
                        setCreateMaterialsPopup={setShowCreateChapterPopUp}
                        chapter_x_subject_id={inputs.chapterId}
                        course_id={inputs.courseId}
                        setChapterMaterials={setChapterMaterials}
                    />
                )}

                <div className="ModeratorMaterialsPageBodyTop">
                    <div className="ModeratorMaterialsPageBodyTopHelpBtnContainer">
                        <Help className="ModeratorMaterialsPageBodyTopHelpBtn" />
                    </div>

                    <>
                        {showCreateChapterPopUpBtn && (
                            <div
                                className="ModeratorMaterialsPageBodyTopBtn"
                                onClick={() => setShowCreateChapterPopUp(true)}
                            >
                                Create New Materials
                            </div>
                        )}
                    </>
                </div>
            </div>

            <div className="ChapterMaterialsTable">
                {chapterMaterials?.chapterMaterials.length > 0 && (
                    <ModMaterialsItems isheader={true} />
                )}
                {chapterMaterials.chapterMaterials.length > 0 ? (
                    chapterMaterials.chapterMaterials.map((material) => {
                        return (
                            <ModMaterialsItems
                                key={material.id}
                                subject_name={
                                    chapterMaterials.chapterData.subject
                                        .subject_name
                                }
                                chapter_name={
                                    chapterMaterials.chapterData.chapter_name
                                }
                                chapter_serial={
                                    chapterMaterials.chapterData.chapter_serial
                                }
                                file_name={
                                    material.materials.file_path
                                        .split("/")
                                        [
                                            material.materials.file_path.split(
                                                "/"
                                            ).length - 1
                                        ].split("--")[1]
                                }
                                file_type={material.materials.file_type}
                                size={material.materials.size / 1000}
                                updated_at={material.materials.updated_at}
                                handleDelete={(e) => {
                                    handleDelete(
                                        e,
                                        material.id,
                                        material.chapter_x_subject_id
                                    );
                                }}
                                handleDownload={(e) => {
                                    // console.log(material.materials.file_type);
                                    var data =
                                        material.materials.file_path.split("/");
                                    var fileName = data[data.length - 1];
                                    handleDownloadRequest(
                                        e,
                                        material.materials.file_path,
                                        fileName
                                    );
                                }}
                            />
                        );
                    })
                ) : (
                    <div>
                         Please select Class, Subject and a chapter for
                        materials
                    </div>
                )}
            </div>
        </div>
    );
}

export default ModeratorMaterialPage;
