import React, { useState } from "react";

const ColorSelectBtn = ({
    backgroundColor,
    options,
    setOptions,
    text,
    isDisabled,
}) => {
    const [selected, setSelected] = useState(false);

    return (
        <button
            disabled={isDisabled}
            className="optionBtn"
            style={{
                backgroundColor,
                border: selected ? "1px solid black" : "",
            }}
            onClick={() => {
                options(setOptions);
                setSelected(!selected);
            }}
        >
            {text}
        </button>
    );
};

export default ColorSelectBtn;
