import { Close, InfoOutlined } from "@material-ui/icons";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { createCourse } from "../../../../../api/course.api";
import "../../popUps/popUp.scss";

function CreateCoursePopUp(props) {
    const { setCreateCoursePopUpOpen, subjects, token } = props;

    const [inputOneSelected, setInputOneSelected] = useState(false);
    const [inputTwoSelected, setInputTwoSelected] = useState(false);
    const [inputs, setInputs] = useState({
        subjectId: "",
        useStandardMaterial: false,
    });
    const [subjectError, setSubjectError] = useState("");
    const [standardMaterialError, setStandardMaterialError] = useState("");

    const handleSubjectFilters = async (e) => {
        const subjectId = e.target.value;
        setInputs({ ...inputs, subjectId: subjectId });
    };

    const handleCreateCourse = () => {
        if (inputs.subjectId === "") {
            setSubjectError("Please select a subject");
            return;
        }
        if (!inputOneSelected && !inputTwoSelected) {
            setStandardMaterialError(
                "Please specify if you want to use standard TSC material"
            );
            return;
        }
        if (inputOneSelected) {
            setSubjectError("");
            inputs.useStandardMaterial = true;
            setStandardMaterialError("");
        }
        if (inputTwoSelected) {
            setSubjectError("");
            inputs.useStandardMaterial = false;
            setStandardMaterialError("");
        }

        setSubjectError("");
        setStandardMaterialError("");
        console.log(inputs);
        createCourse({
            subjectId: inputs.subjectId,
            useStandardMaterial: inputs.useStandardMaterial,
            token: token,
        })
            .then((res) => {
                console.log("Course created");
                setCreateCoursePopUpOpen(false);
                props.setReload(!props.reload);
            })
            .catch((err) => {
                console.log(err);
            });
    };

    return (
        <div className="popup_container">
            <div className="action_row">
                <div className="action_row_left">
                    <InfoOutlined
                        onClick={() => {
                            alert("This is the create course pop up");
                        }}
                        style={{
                            fontSize: "2.5rem",
                            cursor: "pointer",
                        }}
                    />
                </div>
                <div className="action_row_right">
                    <Close
                        onClick={() => {
                            setCreateCoursePopUpOpen(false);
                        }}
                        style={{
                            fontSize: "2.5rem",
                            cursor: "pointer",
                        }}
                    />
                </div>
            </div>

            <div className="popup_title">
                <h2>Create Course</h2>
            </div>

            <div>
                <select
                    name="class"
                    onChange={handleSubjectFilters}
                    style={{
                        height: "50px",
                        marginTop: "10px",
                        border: "none",
                    }}
                    defaultValue="default"
                >
                    <option value="default" disabled>
                        Select Subject
                    </option>
                    {subjects.length > 0 &&
                        subjects.map((subjectItem) => {
                            return (
                                <option
                                    key={subjectItem.id}
                                    value={subjectItem.subject.id}
                                >
                                    {subjectItem.subject.subject_name}
                                </option>
                            );
                        })}
                </select>
                <p style={{ color: "red" }}>{subjectError}</p>
            </div>

            <div className="popup_subtitle">
                <h3>Do you want to use TSC Standard Materials?</h3>
            </div>

            <div className="popup_choice_col">
                <div className="popup_choice_row">
                    <div className="popup_choice">
                        <input
                            type="checkbox"
                            title="Yes"
                            className="popup_check"
                            checked={inputOneSelected}
                            onChange={() => {
                                setInputOneSelected(!inputOneSelected);
                                setInputTwoSelected(false);
                            }}
                        />
                        <label className="popup_label">Yes</label>
                    </div>

                    <div className="popup_choice">
                        <input
                            type="checkbox"
                            title="Yes"
                            className="popup_check"
                            checked={inputTwoSelected}
                            onChange={() => {
                                setInputTwoSelected(!inputTwoSelected);
                                setInputOneSelected(false);
                            }}
                        />
                        <label className="popup_label">No</label>
                    </div>
                </div>

                <div className="popup_error">
                    {standardMaterialError && <p>{standardMaterialError}</p>}
                </div>
            </div>

            <div className="popup_btn_row">
                <span className="popup_btn" onClick={handleCreateCourse}>
                    Create
                </span>

                <span
                    className="popup_btn"
                    onClick={() => {
                        setCreateCoursePopUpOpen(false);
                    }}
                >
                    Cancel
                </span>
            </div>
        </div>
    );
}

export default CreateCoursePopUp;
