import { Add, CalendarToday, Edit } from "@material-ui/icons";
import ReactTooltip from "react-tooltip";
import React, { useState, useEffect } from "react";

import { loadStorage } from "../../../utils/persistLocalStorage";
import "./homePage.scss";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { RoutineMaker } from "../components/routine/routineMaker/RoutineMaker";
import { useNavigate } from "react-router-dom";
import UpdateRoutine from "../components/routine/updateRoutine/UpdateRoutine";

function HomePage() {
	const [subjects, setSubjects] = useState([]);
	const [value, onChange] = useState(new Date());
	const [editRoutine, setEditRoutine] = useState(false);
	var user = loadStorage("user");
	const navigate = useNavigate();

	useEffect(() => {
		user = loadStorage("user");
		if (!user) {
			navigate("/");
		}
	}, []);

	return (
		<div className="homepage_container">
			{/* top section */}
			<div className="homepage_title">
				<p className="homepage_title_text">Teachers Dashboard</p>
			</div>
			{/* info section */}
			<div className="homepage_info">
				<div className="teacher_details">
					<div className="teacher_details_top_row">
						<div className="teacher_details_name">
							<p className="">
								Hi{" "}
								{user
									? user.first_name + " " + user.last_name
									: ""}
							</p>
						</div>
						<div className="teacher_details_xp">
							<span className="teacher_details_xp_level">
								Level: 1
							</span>
							<span className="teacher_details_xp_count">
								2400 XP
							</span>
						</div>
					</div>
				</div>
				<div className="notifications">
					<div className="notification_title">
						<div className="notification_title_left">
							<div className="notification_icon">
								<CalendarToday />
							</div>
							<span>Calender</span>
						</div>
						<div className="notification_title_right">
							{/* <Link
                                to="/dashboard"
                                className="notification_title_right_text"
                            >
                                See All
                            </Link> */}
						</div>
					</div>
					<Calendar
						onChange={onChange}
						value={value}
						className="reactCalendar"
					/>
				</div>
			</div>
			{/* classes section */}
			<div className="homepage_grid">
				<div className="fev_classes">
					<div className="title">
						Classes
						<button className="add_button">
							<Add /> Add Class
						</button>
					</div>
				</div>
				<div className="notification_container">
					<ReactTooltip
						id="EDITMODE"
						place="bottom"
						type="dark"
						effect="solid"
					>
						Click here to edit the routine
					</ReactTooltip>
					<div
						className="exam_notification_container"
						style={{
							border: editRoutine
								? "1px solid #e05353"
								: "1px solid #ffffff",
						}}
					>
						<div className="title">
							Routine
							<div className="add_button">
								{editRoutine && (
									<div
										style={{
											color: "#e05353",
										}}
									>
										Update Routine
									</div>
								)}

								<Edit
									data-tip
									data-for="EDITMODE"
									style={{
										color: "#fff",
										backgroundColor: editRoutine
											? "#e05353"
											: "#00bcd4",
										borderRadius: "50%",
										padding: "10px",
										marginLeft: "10px",
										cursor: "pointer",
									}}
									onClick={() => setEditRoutine(!editRoutine)}
								/>
							</div>
						</div>

						<RoutineMaker user={user} editRoutine={editRoutine} />

						{editRoutine && (
							<UpdateRoutine
								user={user}
								editRoutine={editRoutine}
								setEditRoutine={setEditRoutine}
							/>
						)}
					</div>
				</div>
			</div>
		</div>
	);
}

export default HomePage;
