import React from "react";
import "./modHeader.scss";

function ModHeader({ showNav, setShowNav }) {
	const toggleNav = () => {
		setShowNav(!showNav);
	}
	return (
		<div className="mod_header_container">
			<div className="mod_navbar_header">
				<img
					className="logo"
					src={require("../../../../media/images/TSCDemoLogo.png")}
					alt="img"
				/>
				<div className="title">iShkool Moderator Panel</div>
			</div>

			<div className="hamburger_menu" onClick={toggleNav}>
				<div className="hamburger_menu_icon">
					<div className="hamburger_menu_icon_line"></div>
					<div className="hamburger_menu_icon_line"></div>
					<div className="hamburger_menu_icon_line"></div>
				</div>
			</div>
		</div>
	);
}

export default ModHeader;
