import React, { useEffect, useState } from "react";

import "./moderatorDashboard.scss";
import ModNavbar from "../components/ModNavbar/ModNavbar";
import { Outlet } from "react-router-dom";
import { saveStorage } from "../../../utils/persistLocalStorage";
import ModHeader from "../components/ModHeader/ModHeader";
import ModMobileNavbar from "../components/ModMobileNavbar/ModMobileNavbar";

function getWindowDimensions() {
    const { innerWidth: width, innerHeight: height } = window;
    return {
        width,
        height,
    };
}

export function useWindowDimensions() {
    const [windowDimensions, setWindowDimensions] = useState(
        getWindowDimensions()
    );

    useEffect(() => {
        function handleResize() {
            setWindowDimensions(getWindowDimensions());
        }

        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, []);

    return windowDimensions;
}

const ModeratorDashbord = () => {
    // const [activePage, setActivePage] = useState("#Classes");

    const [showNav, setShowNav] = useState(false);
    // var { innerWidth: width, innerHeight: height } = window;
    const { height, width } = useWindowDimensions();

    useEffect(() => {
        saveStorage("currentPage", "Overview");
    }, []);

    return (
        <div className="Moderator_Dashbord_Container">
            <ModHeader showNav={showNav} setShowNav={setShowNav} />
            <div className="Moderator_Dashbord_Content">
                {showNav && (
                    <div className="Moderator_Dashbord_Header">
                        {width < 600 ? <ModMobileNavbar /> : <ModNavbar />}
                    </div>
                )}
                <div className="Moderator_Dashbord_Body">
                    <Outlet />
                </div>
            </div>
        </div>
    );
};

export default ModeratorDashbord;
