import {
	AddTwoTone,
	ChevronLeft,
	CloudDownloadOutlined,
	KeyboardArrowDown,
	DeleteForeverOutlined,
} from "@material-ui/icons";

import { CircularProgressbar } from "react-circular-progressbar";
import axios from "axios";
import fileDownload from "js-file-download";
import React, { useEffect, useState } from "react";
import { getModeratorChapterInfoByChapterId } from "../../../../api/chapter.api";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import { MAT_URL } from "../../../../utils/urls";
import { BookUploadMod } from "./components/BookUploadMod";
import FileDownloadPage from "./FileDownloadPage";
import "./ModeratorTopicDetails.scss";
import MaterialsPopUp from "../../components/popUps/materialsPopUps/CreateMaterialsPopUp";

const ModeratorTopicDetails = (props) => {
	console.log("material props =>", props);
	const [materials, setMaterials] = useState([]);
	const [openMaterials, setOpenMaterials] = useState(true);
	const [showBookUploadFile, setShowBookUploadFile] = useState(false);
	const [showBookList, setShowBookList] = useState(true);
	const [update, setUpdate] = useState(false);

	const [chapterId, setChapterId] = useState(props.chapter.id);
	const [subjectBook, setSubjectBook] = useState();
	const [downloadPercentage, setDownloadPercentage] = useState();
	const [showCreateChapterPopUp, setShowCreateChapterPopUp] = useState(false);

	const [chapterMaterials, setChapterMaterials] = useState({
		chapterData: {},
		chapterMaterials: [],
	});

	const [isLoading, setIsLoading] = useState(true);

	var user = loadStorage("user");

	const handleShowBookUploadFile = () => {
		setShowBookUploadFile(!showBookUploadFile);
	};

	const toggolSetUpdate = () => {
		setUpdate(!update);
	};

	useEffect(() => {
		user = loadStorage("user");
		setChapterId(props.chapter.id);
		getModeratorChapterInfoByChapterId({ chapterId, token: user.token })
			.then((res) => {
				setChapterMaterials({
					chapterData: res.data.data.chapter.chapterData,
					chapterMaterials: res.data.data.chapter.chapterMaterials,
				});
				console.log(res);
				setMaterials(res.data.data.chapter.chapterMaterials);
				setSubjectBook(res.data.data.chapter.chapterBooks);
				// console.log("=>", materials);
				console.log("=>", subjectBook);
				setIsLoading(false);
			})
			.catch((err) => {
				console.log(err);
				setIsLoading(false);
			});
	}, [props, showCreateChapterPopUp, update]);

	const handleBookDownloadRequest = async (filePath, filename) => {
		var fullPath = MAT_URL + filePath;

		axios({
			method: "get",
			url: fullPath,
			responseType: "blob",
			onDownloadProgress: (progressEvent) => {
				let percentCompleted = Math.round(
					(progressEvent.loaded * 100) / progressEvent.total
				);
				setDownloadPercentage(percentCompleted);
			},
		})
			.then((res) => {
				console.log(res);
				fileDownload(res.data, filename);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const formatDateTime = (dueDateTime) => {
		const date = new Date(dueDateTime);
		const day = date.getDate();
		const monthList = [
			"Jan",
			"Feb",
			"Mar",
			"Apr",
			"May",
			"Jun",
			"July",
			"Aug",
			"Sep",
			"Oct",
			"Nov",
			"Dec",
		];
		const textMonth = monthList[date.getMonth()];
		const year = date.getFullYear();
		const hour = date.getHours();
		const minute = date.getMinutes();
		const ampm = hour >= 12 ? "PM" : "AM";
		const hour12 = hour % 12;
		const sHours = hour12 < 10 ? `0${hour12}` : hour12;
		const sMinutes = minute < 10 ? `0${minute}` : minute;
		const formattedTime = `${sHours}:${sMinutes} ${ampm}`;
		return `${day} ${textMonth}, ${year} ${formattedTime}`;
	};

	return (
		<div className="popup-box">
			<div className="box">
				{showCreateChapterPopUp && (
					<MaterialsPopUp
						setCreateMaterialsPopup={setShowCreateChapterPopUp}
						chapter_x_subject_id={chapterId}
						course_id={props.classItem.id}
						setChapterMaterials={setChapterMaterials}
					/>
				)}
				<span className="close-icon" onClick={props.handleClose}>
					x
				</span>

				<div className="popup-box-materials-container">
					<div className="topic_header">
						<div
							className="topic_header_Image"
							style={{
								backgroundImage: `url(${
									MAT_URL +
									props.chapter.chapter_cover.file_path
								})`,

								backgroundSize: "contain",
								backgroundPosition: "center",
								backgroundRepeat: "no-repeat",
							}}
						>
							<div className="topic_header_course_code">
								{props.subjectItem.course_code}
							</div>

							<div className="topic_header_Icon">
								<img
									src={
										MAT_URL +
										props.chapter.chapter_icon.file_path
									}
									alt="chapter_icon"
								/>

								<div className="topic_header_info_left">
									<div className="topic_header_className">
										{props.classItem.class_name}
									</div>
									<div className="topic_header_subject_name">
										{props.subjectItem.subject.subject_name}{" "}
										- {props.chapter.chapter_name}
									</div>
									<div className="topic_header_chapter_info">
										chapter serial :{" "}
										{props.chapter.chapter_serial}
										{" | "}
										chapter id : {props.chapter.id}
									</div>
								</div>
								<div className="topic_header_info_right"> </div>
							</div>
						</div>
					</div>
				</div>

				<div className="topic_header_chapter_Materials">
					<div className="topic_header_chapter_Materials_header">
						<div className="topic_header_chapter_Materials_header_title">
							<div className="title">Chapter Materials</div>
							<div className="white_thin_line" />

							<div
								className="white_thin_icons"
								onClick={() => {
									setOpenMaterials(!openMaterials);
								}}
							>
								{openMaterials ? (
									<KeyboardArrowDown />
								) : (
									<ChevronLeft />
								)}
							</div>

							<AddTwoTone
								style={{
									fontSize: "30px",
									paddingLeft: "20px",
									paddingRight: "20px",
									color: "black",
									cursor: "pointer",
								}}
								onClick={() => setShowCreateChapterPopUp(true)}
							/>
						</div>

						{!isLoading && openMaterials ? (
							materials.map((material, index) => {
								return (
									<div
										className="topic_header_chapter_Materials_header_item"
										key={index}
									>
										<FileDownloadPage
											material={material}
											toggolSetUpdate={toggolSetUpdate}
										/>
									</div>
								);
							})
						) : (
							<div
								className="topic_header_chapter_Materials_header_item"
								onClick={() => {
									setOpenMaterials(!openMaterials);
								}}
							>
								Materials table is collapsed
							</div>
						)}
					</div>

					<div className="topic_header_chapter_Materials_header">
						<div className="topic_header_chapter_Materials_header_title">
							<div className="title">Books</div>
							<div className="white_thin_line" />

							<div
								className="white_thin_icons"
								onClick={() => {
									setShowBookList(!showBookList);
								}}
							>
								{showBookList ? (
									<KeyboardArrowDown />
								) : (
									<ChevronLeft />
								)}
							</div>

							<AddTwoTone
								style={{
									fontSize: "30px",
									paddingLeft: "10px",
									paddingRight: "20px",
									color: "black",
									cursor: "pointer",
								}}
								onClick={handleShowBookUploadFile}
							/>
						</div>

						{!isLoading && showBookList ? (
							subjectBook ? (
								<div>
									{subjectBook.map((item, index) => {
										return (
											<div
												className="BookInfocard"
												key={index}
											>
												<div>
													<img
														src={
															MAT_URL +
															item.thumb.file_path
														}
														alt="book_thumb"
														style={{
															width: "100px",
															ObjectFit: "cover",
															border: "5px solid #ccc",
														}}
													/>
												</div>
												<div className="bookInfoRowMid">
													<div>
														File Name :{" "}
														{
															item.books.file_path
																.split("/")
																[
																	item.books.file_path.split(
																		"/"
																	).length - 1
																].split("--")[1]
														}
													</div>
													<div>
														File Size :{" "}
														{(
															item.books.size /
															1024 /
															1024
														).toFixed(2)}{" "}
													</div>
													<div>
														Uploaded at:{" "}
														{formatDateTime(
															item.updated_at
														)}
													</div>
												</div>
												<div>
													<CloudDownloadOutlined
														className="bookDOwnloadIcon"
														onClick={() => {
															handleBookDownloadRequest(
																item.books
																	.file_path,
																item.books.file_path
																	.split("/")
																	[
																		item.books.file_path.split(
																			"/"
																		)
																			.length -
																			1
																	].split(
																		"--"
																	)[1]
															);
														}}
													/>
												</div>
												<div
													style={{
														display: "flex",
														justifyContent:
															"center",
														alignItems: "center",
														paddingRight: "20px",
														paddingLeft: "20px",
													}}
												>
													<DeleteForeverOutlined
														style={{
															fontSize: "40px",
														}}
														onClick={() => {}}
													/>
												</div>
											</div>
										);
									})}
								</div>
							) : (
								<div className="topic_header_chapter_Materials_header_item">
									No Books Available
								</div>
							)
						) : (
							<div
								className="topic_header_chapter_Materials_header_item"
								onClick={() => {
									setShowBookList(!showBookList);
								}}
							>
								Books table is collapsed
							</div>
						)}

						{showBookUploadFile && (
							<BookUploadMod
								handleShowBookUploadFile={
									handleShowBookUploadFile
								}
								chapter_x_subject_id={chapterId}
								course_id={props.subjectItem.id}
								user={user}
							/>
						)}
					</div>
					<div className="topic_header_chapter_Materials_header header">
						Quizes
					</div>
				</div>
			</div>
		</div>
	);
};

export default ModeratorTopicDetails;
