import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import "./moderatorSubjectPage.scss";
import { getEnabledSubjectsByClassID } from "../../../../api/subject.api";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import ModPageHeader from "../../components/ModPageHeader/ModPageHeader";
import ModeratorSubjectListItem from "../../components/ModeratorSubjectListItem/ModeratorSubjectListItem";
import { getAllCLasses } from "../../../../api/class.api";
import CreateSubjectPopUp from "../../components/popUps/subjectPopUps/CreateSubjectPopUp";
import ModButton from "../../components/ModButton/ModButton";
import UpdateSubjectPopUp from "../../components/popUps/subjectPopUps/UpdateSubjectPopUp";

function ModeratorSubjectPage() {
	var user = loadStorage("user");
	const navigate = useNavigate();

	const idCreatePopUp = "create_subject_popup";
	const idUpdatePopUp = "update_subject_popup";
	const idDeletePopUp = "delete_subject_popup";
	const [createSubjectPopUpOpen, setCreateSubjectPopUpOpen] = useState(false);
	const [updateSubjectPopUpOpen, setUpdateSubjectPopUpOpen] = useState(false);
	const [deleteSubjectPopUpOpen, setDeleteSubjectPopUpOpen] = useState(false);

	const [isLoading, setIsLoading] = useState(true);
	const [classes, setClasses] = useState([]);
	const [subjects, setSubjects] = useState([]);
	const [singleSubject, setSingleSubject] = useState({});
	const [inputs, setInputs] = useState({
		classId: "",
	});
	var user = loadStorage("user");

	useEffect(() => {
		if (!user) {
			navigate("/auth/login");
		} else if (classes.length === 0) {
			getAllCLasses({ token: user.token })
				.then((res) => {
					console.log(res.data.data);
					setClasses(res.data.data);
					handleClassFilters(res.data.data[0].id);
				})
				.catch((err) => {
					console.log(err);
				});
		}
	}, [classes]);

	const handleClassFilters = async (classId) => {
		console.log(classId);
		setInputs({ ...inputs, classId });

		getEnabledSubjectsByClassID({ classId, token: user.token })
			.then((res) => {
				console.log(res.data.data);
				setSubjects(res.data.data);
				setIsLoading(false);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const addNewSubject = () => {
		setCreateSubjectPopUpOpen(true);
	};

	const updateSubject = (subjectItem) => {
		console.log(subjectItem);
		setSingleSubject(subjectItem.subject);
		setUpdateSubjectPopUpOpen(true);
	};

	const deleteSubject = (id) => {
		console.log("delete subject");
		console.log(id);
	};

	return isLoading ? (
		<div className="loading-container">
			<div className="loading"></div>
		</div>
	) : (
		<div className="ModeratorPageContainer">
			<div className="ModeratorPageHeader">
				<div className="ModeratorPageHeaderFilterContainer">
					<select
						className="ModeratorPageHeaderFilterButton"
						defaultValue={classes.length > 0 ? 1 : "default"}
						onChange={(e) => {
							handleClassFilters(e.target.value);
						}}
					>
						<option value="default" disabled>
							Select Class
						</option>
						{classes.length > 0 &&
							classes.map((classItem) => {
								console.log(classItem);
								return (
									<option
										key={classItem.id}
										value={classItem.id}
									>
										 {classItem.class_name}
									</option>
								);
							})}
					</select>
				</div>

				<div className="ModeratorPageHeaderButtonArea">
					{inputs.classId ? (
						<ModButton
							btnText={"Add New Subject"}
							onClick={addNewSubject}
						/>
					) : (
						"You can add new subject after selecting a class"
					)}
				</div>
			</div>
			<div className="ModeratorPageBody">
				<div className="ModeratorPageBodyTable">
					{subjects.length > 0 ? (
						<ModPageHeader
							items={["Icon", "Subject Name", "Action"]}
						/>
					) : (
						<ModPageHeader
							items={[
								" No Subjects Found",
								"Please select a Class",
							]}
						/>
					)}
					<div className="ModeratorPageBodyTableRow">
						{subjects.map((subjectItem, index) => {
							return (
								<ModeratorSubjectListItem
									key={index}
									subjectItem={subjectItem}
									updateSubject={updateSubject}
									deleteSubject={deleteSubject}
								/>
							);
						})}
					</div>
				</div>
			</div>
			{createSubjectPopUpOpen && (
				<CreateSubjectPopUp
					className="modal fade"
					id={idCreatePopUp}
					setCreateSubjectPopUpOpen={setCreateSubjectPopUpOpen}
					classId={inputs.classId}
					setSubjects={setSubjects}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}

			{updateSubjectPopUpOpen && (
				<UpdateSubjectPopUp
					className="modal fade"
					id={idUpdatePopUp}
					setUpdateSubjectPopUpOpen={setUpdateSubjectPopUpOpen}
					subjectItem={singleSubject}
					setSubjects={setSubjects}
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				/>
			)}
		</div>
	);
}

export default ModeratorSubjectPage;
