import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Close, InfoOutlined } from "@material-ui/icons";
import "../../popUps/popUp.scss";
import { loadStorage } from "../../../../../utils/persistLocalStorage";

function CreateBookPopUp(props) {
	const { setCreateBookPopupOpen, chapter_x_subject_id, course_id } = props;
	console.log(props);

	const navigate = useNavigate();
	var user = loadStorage("user");

	useEffect(() => {
		user = loadStorage("user");
		if (!user || !user.token) {
			navigate("/auth/login");
		}
	}, [user]);

	const [selectedFile1, setSelectedFile1] = useState();
	const [selectFileError, setSelectFileError] = useState("");

	const changeHandler1 = (event) => {
		setSelectedFile1(event.target.files[0]);
	};

	const uploadBook = () => {
		console.log("upload book");
		if (!selectedFile1) {
			setSelectFileError("Please select a file");
			return;
		} else {
			setSelectFileError("");
			const formData = new FormData();
			formData.append("chapter_x_subject_id", chapter_x_subject_id);
			formData.append("course_id", course_id);
			formData.append("chapter_book", selectedFile1);
			console.log(formData);

			// axios({
			// 	method: "post",
			// 	url: CREATE_MATERIALS_URL,
			// 	headers: {
			// 		"x-auth-token": user.token,
			// 		"Content-Type": "multipart/form-data",
			// 	},
			// 	data: formData,
			// })
			// 	.then((res) => {
			// 		alert("Book upload completed");
			// 		setCreateMaterialsPopupOpen(false);
			// 		window.location.reload();
			// 	})
			// 	.catch((err) => {
			// 		console.log(err);
			// 	});
		}
	};

	return (
		<div className="popup_container">
			<div className="action_row">
				<div className="action_row_left">
					<InfoOutlined
						onClick={() => {
							alert("This is the create course pop up");
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
				<div className="action_row_right">
					<Close
						onClick={() => {
							setCreateBookPopupOpen(false);
						}}
						style={{
							fontSize: "2.5rem",
							cursor: "pointer",
						}}
					/>
				</div>
			</div>

			<div className="popup_title">
				<h2>Upload Book</h2>
			</div>

			<div className="form">
				<div className="popup_choice_row">
					<div className="popup_choice">
						<input
							type="file"
							title="Book"
							required
							onChange={changeHandler1}
						/>
					</div>
				</div>

				<div className="popup_error">
					{setSelectFileError && <p>{selectFileError}</p>}
				</div>
			</div>

			<div className="popup_btn_row">
				<span className="popup_btn" onClick={uploadBook}>
					Create
				</span>

				<span
					className="popup_btn"
					onClick={() => {
						setCreateBookPopupOpen(false);
					}}
				>
					Cancel
				</span>
			</div>
		</div>
	);
}

export default CreateBookPopUp;
