import React, { useEffect, useRef, useState } from "react";
import { Close, InfoOutlined } from "@material-ui/icons";
import { useNavigate } from "react-router-dom";
import axios, { CancelToken, isCancel } from "axios";
import "./../popUp.scss";
import ModButton from "../../ModButton/ModButton";
import { loadStorage } from "../../../../../utils/persistLocalStorage";
import { getModeratorChapterInfoByChapterId } from "../../../../../api/chapter.api";
import { CREATE_MATERIALS_URL } from "../../../../../utils/urls";
import ProgressBar from "@ramonak/react-progress-bar";
import { PopUpPromt } from "../../popUpPromt/PopUpPromt";

function MaterialsPopUp(props) {
    const [selectedFile1, setSelectedFile1] = useState();
    const [selectFileError, setSelectFileError] = useState("");
    const [uploadProgress, setUploadProgress] = useState(0);
    const [uploading, setUploading] = useState(false);
    const cancelFileUpload = useRef(null);

    // let cancelTokenSource = CancelToken.source();

    const {
        setCreateMaterialsPopup,
        chapter_x_subject_id,
        setChapterMaterials,
    } = props;
    console.log(props);

    const navigate = useNavigate();
    var user = loadStorage("user");

    useEffect(() => {
        user = loadStorage("user");
        if (!user || !user.token) {
            navigate("/auth/login");
        }
    }, [user]);

    const changeHandler1 = (event) => {
        setSelectedFile1(event.target.files[0]);
    };

    const handleSubmit = () => {
        console.log("create Materials");
        if (!selectedFile1) {
            setSelectFileError("Please select a file");
            return;
        } else {
            setUploading(true);
            const config = {
                headers: {
                    "x-auth-token": user.token,
                    "Content-Type": "multipart/form-data",
                },
                onUploadProgress: function (progressEvent) {
                    console.log(progressEvent);

                    var percentCompleted = Math.round(
                        (progressEvent.loaded * 100) / progressEvent.total
                    );
                    console.log("Upload progress=>", percentCompleted);
                    setUploadProgress(percentCompleted);
                },
                cancelToken: new CancelToken(cancel => cancelFileUpload.current = cancel),
            };
            setSelectFileError("");
            const formData = new FormData();
            formData.append("chapter_x_subject_id", props.chapter_x_subject_id);
            formData.append("course_id", props.course_id);
            formData.append("chapter_material", selectedFile1);
            console.log(formData);

            axios
                .post(CREATE_MATERIALS_URL, formData, config)
                .then((res) => {
                    getModeratorChapterInfoByChapterId({
                        chapterId: chapter_x_subject_id,
                        token: user.token,
                    })
                        .then((res) => {
                            setChapterMaterials({
                                chapterData: res.data.data.chapter.chapterData,
                                chapterMaterials:
                                    res.data.data.chapter.chapterMaterials,
                            });
                        })
                        .catch((err) => {
                            console.log(err);
                        });
                    setCreateMaterialsPopup(false);
                })
                .catch((err) => {
                    console.log(err);
                });
        }
    };

    const cancelUpload = () => {
        if (cancelFileUpload.current) {
            cancelFileUpload.current("Upload canceled");
        }
        setUploading(false);
        setUploadProgress(0);
    };

    return (
        <div className="ModeratorFormContainer">
            <div className="action_row">
                <div className="action_row_left">
                    <InfoOutlined
                        onClick={() => {
                            alert("This is the create course pop up");
                        }}
                        style={{
                            fontSize: "2.5rem",
                            cursor: "pointer",
                        }}
                    />
                </div>
                <div className="action_row_right">
                    <Close
                        onClick={() => {
                            setCreateMaterialsPopup(false);
                        }}
                        style={{
                            fontSize: "2.5rem",
                            cursor: "pointer",
                        }}
                    />
                </div>
            </div>

            <div className="ModeratorFormInputsContainer">
                <div className="form_row">
                    <div className="form_field">
                        <label>Select Material</label>
                        <input
                            type="file"
                            name="class_cover"
                            required
                            className="ModeratorFormInput"
                            onChange={changeHandler1}
                        />
                        <div className="error">{selectFileError}</div>
                    </div>
                </div>
                {uploading && (
                    <div className="progressRow">
                        <div
                            style={{
                                padding: "5px",
                            }}
                        >
                            File Upload in Progress
                        </div>
                        <ProgressBar
                            completed={uploadProgress}
                            labelAlignment="center"
                        />
                    </div>
                )}

                <div className="ModeratorFormBtns">
                    <ModButton btnText={"Create"} onClick={handleSubmit} />
                    {/* {uploading ? (
                        <PopUpPromt
                            onClick={() => {
                                setCreateMaterialsPopup(false);
                            }}
                        />
                    ) : ( */}
                        <ModButton
                            btnText="Cancel"
                            onClick={() => {
                                cancelUpload();
                                setCreateMaterialsPopup(false);
                            }}
                        />
                    {/* )} */}
                </div>
            </div>
        </div>
    );
}

export default MaterialsPopUp;
