import React, { useEffect, useState } from "react";
import "./formTitleDescription.scss";

function FormTitleDescription({
	formTitle,
	formDescription,
	formChapter,
	formCourse,
	formDue,
	formPassMark,
	formTotalmarks,
	handleChange,
	handleUpdateInfo,
}) {
	const [dueDateTime, setDueDateTime] = useState();

	useEffect(() => {
		if (formDue !== null) {
			const date = new Date(formDue);
			const day = date.getDate();
			const monthList = [
				"Jan",
				"Feb",
				"Mar",
				"Apr",
				"May",
				"Jun",
				"Jul",
				"Aug",
				"Sep",
				"Oct",
				"Nov",
				"Dec",
			];
			const textMonth = monthList[date.getMonth()];
			const year = date.getFullYear();
			const hour = date.getHours();
			const minute = date.getMinutes();
			const ampm = hour >= 12 ? "PM" : "AM";
			const hour12 = hour % 12;
			const sHours = hour12 < 10 ? `0${hour12}` : hour12;
			const sMinutes = minute < 10 ? `0${minute}` : minute;
			const formattedTime = `${sHours}:${sMinutes} ${ampm}`;
			setDueDateTime(`${day} ${textMonth}, ${year} ${formattedTime}`);
		}
	}, [dueDateTime]);

	return (
		<div className="FormTitleDescription_container">
			<div className="form_title_description_title">
				<input
					type="text"
					name="title"
					placeholder="Form title"
					value={formTitle}
					onChange={handleChange}
				/>
			</div>
			<div className="form_title_description_description">
				<textarea
					name="description"
					placeholder="Form description"
					value={formDescription}
					onChange={handleChange}
				></textarea>
			</div>
			<hr />
			<div className="class_info_container">
				<div className="class_info_item">
					<span>Class:</span>
					{formCourse.subject.class.class_name}
				</div>
				<div className="class_info_item">
					<span>Subject:</span>
					{formCourse.subject.subject_name}
				</div>
				<div className="class_info_item">
					<span>Chapter:</span>
					{formChapter.chapter_name}
				</div>
				<div className="class_info_item"></div>
				<div className="class_info_item">
					<span>Due Date-Time:</span>
					<div
						style={{
							display: "flex",
							flexDirection: "column",
						}}
					>
						<div>{formDue !== null ? dueDateTime : "N/A"}</div>
						<div>
							<input
								id="due_date_time"
								name="due_date_time"
								type="datetime-local"
								onChange={handleChange}
							/>
						</div>
					</div>
				</div>
				<div className="class_info_item"></div>
				<div className="class_info_item">
					<span>Pass Marks:</span>
					<input
						name="pass_marks"
						type="text"
						placeholder="40"
						value={formPassMark}
						onChange={handleChange}
					/>
				</div>
				<div className="class_info_item">
					<span>Total Marks:</span>
					<input
						name="total_marks"
						type="text"
						placeholder="100"
						value={formTotalmarks}
						onChange={handleChange}
					/>
				</div>
			</div>
			<hr />
			<div className="action_container">
				<div>
					<div className="btn" onClick={handleUpdateInfo}>
						<i className="fas fa-save"></i> Update
					</div>
				</div>
			</div>
		</div>
	);
}

export default FormTitleDescription;
