import React from "react";
import { topCourses } from "../../data";
import Course from "./Course";
import "./courses.scss";

const Courses = () => {
	return (
		<div className="CoursesContainer">
			{topCourses.map((items) => (
				<Course key={items.id} item={items} />
			))}
		</div>
	);
};

export default Courses;
