import React from "react";
import { useNavigate } from "react-router-dom";
import "./modNavbarItem.scss";
import { saveStorage } from "../../../../utils/persistLocalStorage";

function ModNavbarItem(props) {
	const { title, link, icon, focused, setCurrentPage } = props;
	console.log(props);

	const navigate = useNavigate();

	const navigateTo = () => {
		saveStorage("currentPage", title);
		navigate(link);
		setCurrentPage(title);
		console.log(title);
	};

	return (
		<div
			className={`mod_navbar_menu_btn ${focused ? " active" : ""}`}
			onClick={navigateTo}
		>
			{icon}
			<div>{title}</div>
		</div>
	);
}

export default ModNavbarItem;
