import axios from "axios";
import {
	CREATE_CLASS,
	DELETE_CLASS,
	GET_ALL_CLASSES,
	UPDATE_CLASS,
} from "../utils/urls";

export const getAllCLasses = (data) => {
	return axios({
		method: "get",
		url: GET_ALL_CLASSES,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
	});
};

export const createClass = (data) => {
	return axios({
		method: "post",
		url: CREATE_CLASS,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
		data: data,
	});
};

export const updateClass = (data) => {
	return axios({
		method: "patch",
		url: UPDATE_CLASS + "/" + data.classId,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
		data: data,
	});
};

export const deleteClass = (data) => {
	return axios({
		method: "delete",
		url: DELETE_CLASS + "/" + data.classId,
		headers: {
			"Content-Type": "application/json",
			"x-auth-token": data.token,
		},
	});
};
