import {
	CheckCircleOutline,
	CloudDownload,
	CloudDownloadOutlined,
	Delete,
	ModeComment,
	PersonAddOutlined,
	ThumbUp,
} from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import "./chapterPage.scss";
import { getTeachersChapterInfoByChapterId } from "../../../../api/course.api";
import { loadStorage } from "../../../../utils/persistLocalStorage";
import Header from "../../components/header/Header";
import CreateMaterialPopUp from "../../components/popUps/createMaterialPopUp/CreateMaterialPopUp";
import axios from "axios";
import fileDownload from "js-file-download";
import { useNavigate } from "react-router-dom";
import CreateBookPopUp from "../../components/popUps/createBookPopUp/CreateBookPopUp";
import { MAT_URL } from "../../../../utils/urls";
import { Slider } from "@material-ui/core";
import ShowMaterials from "../../components/showMaterials/ShowMaterials";
import QuizRow from "../../components/showQuizs/QuizRow";
import ShowBooks from "../../components/showBooks/ShowBooks";
import CreateQuizPopUp from "../../components/popUps/createQuizPopUp/CreateQuizPopUp";

const linkToPPTFile =
	"https://scholar.harvard.edu/files/torman_personal/files/samplepptx.pptx";

// const linkToPPTFile =
// 	"localhost:8000/static/materials/class/2/subject_2/course_3/chapter_3/files/admin/materials/a7921a30-d43c-4255-83af-b382bafe5703--Classroom-Automation.docx";

function ChapterDetails() {
	const user = loadStorage("user");
	const navigate = useNavigate();
	const chapterId = window.location.href.split("/")[4];

	const idUploadMaterialPopUp = "idUploadMaterialPopUp";
	const idUploadBookPopUp = "idUploadBookPopUp";
	const idAddQuizPopUp = "idAddQuizPopUp";
	const [uploadMaterialPopUpOpen, setUploadMaterialPopUpOpen] =
		useState(false);
	const [uploadBookPopUpOpen, setUploadBookPopUpOpen] = useState(false);
	const [addQuizPopUpOpen, setAddQuizPopUpOpen] = useState(false);
	const [isLoading, setIsLoading] = useState(true);
	const [chapterData, setChapterData] = useState({});
	const [courseData, setCourseData] = useState({});
	const [materials, setMaterials] = useState([]);
	const [showQuiz, setShowQuiz] = useState(false);
	const [books, setBooks] = useState([]);
	const [quizs, setQuizs] = useState([]);
	const [sliderHeight, setSliderHeight] = useState(40);

	useEffect(() => {
		if (!user) {
			navigate("/auth/login");
		} else {
			getTeachersChapterInfoByChapterId({
				chapterId,
				token: user.token,
			})
				.then((res) => {
					console.log(res.data.data);
					setChapterData(res.data.data.chapterData);
					setCourseData(res.data.data.courseData);
					setMaterials(res.data.data.chapterMaterials);
					setBooks(res.data.data.chapterBooks);
					setQuizs(res.data.data.chapterQuizes);
					console.log(res.data.data);
					setIsLoading(false);
				})
				.catch((err) => {
					console.log(err);
					setIsLoading(false);
				});
		}
	}, [uploadMaterialPopUpOpen]);

	const uploadMaterial = () => {
		console.log("upload material");
		setUploadMaterialPopUpOpen(true);
	};

	const addNewQuiz = () => {
		console.log("add quiz");
		setAddQuizPopUpOpen(true);
	};

	const createExam = () => {
		console.log("create exam");
	};

	return (
		<div
			style={{
				width: "100vw",
				display: "flex",
				flexDirection: "column",

				alignItems: "center",
			}}
		>
			<div
				style={{
					width: "100%",
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
					marginBottom: "20px",
					boxShadow: "0px 0px 10px #00000029",
				}}
			>
				<Header />
			</div>

			{isLoading ? (
				<div>
					<p
						style={{
							textAlign: "center",
							marginTop: "20px",
							marginBottom: "20px",
							fontWeight: "bold",
							fontSize: "20px",
						}}
					>
						Loading...
					</p>
				</div>
			) : (
				<>
					<div className="coursepage_container">
						<div
							className="goBack_container"
							onClick={() => navigate(-1)}
						>
							 Go Back to course Details
						</div>

						<div className="coursepage_container_header">
							<div className="coursepage_container_Intro_Text">
								<span>
									Class:{" "}
									{chapterData?.subject?.class.class_name}
								</span>
								<span>
									Subject:{" "}
									{chapterData?.subject?.subject_name}
								</span>
								<span>
									Chapter: {chapterData?.chapter_name}
								</span>
							</div>
						</div>

						<div className="coursepage_container_body">
							<div className="class_container_section">
								<div className="section">
									<div className="data_container">
										<div className="section_header">
											<div className="section_header_title">
												Materials
											</div>
											<div
												className="section_header_upload"
												onClick={uploadMaterial}
											>
												Upload Material
											</div>
										</div>

										{/* <div className="presentation_container">
											<div className="Slider_Container">
												<div className="Slider_Container_Text">
													Material Name
												</div>
												<div>Material info</div>
											</div>
											<div className="Slider_Container">
												<div className="Slider_Container_Text">
													Change Window size
												</div>

												<Slider
													style={{
														maxWidth: "200px",
													}}
													size="small"
													defaultValue={40}
													aria-label="Default"
													valueLabelDisplay="auto"
													onChange={(event, value) =>
														setSliderHeight(value)
													}
													min={40}
													max={100}
												/>
											</div>

											<iframe
												src={`https://view.officeapps.live.com/op/embed.aspx?src=${linkToPPTFile}`}
												width="100%"
												height={
													(sliderHeight *
														window.innerHeight) /
													100
												}
												frameBorder="0"
												title="slides"
											></iframe>
										</div> */}

										<div className="section_body">
											{materials.length > 0 ? (
												materials.map((material) => {
													return (
														<div key={material.id}>
															<ShowMaterials
																material={
																	material
																}
																setChapterData={
																	setChapterData
																}
																setCourseData={
																	setCourseData
																}
																setMaterials={
																	setMaterials
																}
																setIsLoading={
																	setIsLoading
																}
																token={
																	user.token
																}
															/>
														</div>
													);
												})
											) : (
												<div className="no_data">
													<p>No Materials</p>
												</div>
											)}
										</div>
									</div>
								</div>
							</div>

							<div className="class_container_section">
								<div className="section">
									<div className="data_container">
										<div className="section_header">
											<div className="section_header_title">
												Books
											</div>
										</div>

										<div className="section_body">
											{books?.length > 0 ? (
												books?.map((book) => {
													return (
														<div key={book.id}>
															<ShowBooks
																material={book}
																setIsLoading={
																	setIsLoading
																}
															/>
														</div>
													);
												})
											) : (
												<div className="no_data">
													<p>No Books</p>
												</div>
											)}
										</div>
									</div>
								</div>
							</div>

							<div className="class_container_section">
								<div className="section">
									<div className="data_container">
										<div className="section_header">
											<div className="section_header_title">
												Quizes
											</div>
											<div
												className="section_header_upload"
												onClick={addNewQuiz}
											>
												Add Quiz
											</div>
										</div>
										<div className="section_body">
											{quizs?.length > 0 ? (
												quizs.map((quiz) => {
													return (
														<div key={quiz.id}>
															<QuizRow
																quiz={quiz}
																setIsLoading={
																	setIsLoading
																}
															/>
														</div>
													);
												})
											) : (
												<div className="no_data">
													<p>No Quizs</p>
												</div>
											)}
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>

					{uploadMaterialPopUpOpen && (
						<CreateMaterialPopUp
							id={idUploadMaterialPopUp}
							setCreateMaterialsPopupOpen={
								setUploadMaterialPopUpOpen
							}
							chapter_x_subject_id={chapterData.id}
							course_id={courseData.id}
						/>
					)}

					{addQuizPopUpOpen && (
						<CreateQuizPopUp
							id={idAddQuizPopUp}
							setCreateQuizPopupOpen={setAddQuizPopUpOpen}
							chapter_x_subject_id={chapterData.id}
							course_id={courseData.id}
						/>
					)}
				</>
			)}
		</div>
	);
}

export default ChapterDetails;
